import axios from "axios";

let baseUrl = null;
let token = null;
//http://localhost:3001
const setEndpoint = (newEndpoint) => {
  baseUrl = `/api/requests/${newEndpoint}`;
  console.log(baseUrl);
};

const setToken = (newToken) => {
  token = `bearer ${newToken}`;
  config.headers.Authorization = token;
};

let config = {
  headers: { Authorization: token },
};

const getAll = async () => {
  const response = await axios.get(baseUrl, config);
  return response.data;
};

const updateStatus = async (status, id) => {
  const response = await axios.put(
    `${baseUrl}/${id}/status`,
    { status },
    config
  );
  return response.data;
};

const getOne = async (id) => {
  const response = await axios.get(`${baseUrl}/${id}`, config);
  return response.data;
};

const createRequest = async (newRequest) => {
  console.log(config);
  const response = await axios.post(baseUrl, newRequest, config);
  return response.data;
};

const updateRequest = async (newRequest) => {
  const response = await axios.put(
    `${baseUrl}/${newRequest._id}`,
    newRequest,
    config
  );
  return response.data;
};

const deleteRequest = async (id) => {
  const config = {
    headers: { Authorization: token },
  };
  const response = await axios.delete(`${baseUrl}/${id}`, config);
  return response.status;
};

const counter = async (endpoint) => {
  const config = {
    headers: { Authorization: token },
  };
  const response = await axios.get(`api/requests/counter/${endpoint}`, config);
  console.log(response.data);
  return response.data;
};
export default {
  getAll,
  updateRequest,
  deleteRequest,
  getOne,
  createRequest,
  setToken,
  setEndpoint,
  updateStatus,
  counter,
};
