import React, { useState, useEffect } from "react";
import requestService from "../../services/requestService";
import Button from "react-bootstrap/Button";
import Spinner from "react-bootstrap/Spinner";
import RequestsTable from "./RequestsTable";
import { Col, Row } from "react-bootstrap";

const RequestsPage = ({ showRequestsForm }) => {
  const [research, setResearch] = useState([]);
  const [expense, setExpense] = useState([]);
  const [purchase, setPurchase] = useState([]);
  const [reallocation, setReallocation] = useState([]);

  const getRequestType = (endpoint, fn) => {
    requestService.setEndpoint(endpoint);
    requestService.getAll().then((reqs) => {
      fn(reqs);
    });
  };

  useEffect(() => {
    requestService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    Promise.all([
      getRequestType("research", setResearch),
      getRequestType("expense", setExpense),
      getRequestType("purchase", setPurchase),
      getRequestType("reallocation", setReallocation),
    ])
      .then((resp) => {
        console.log(resp);
      })
      .catch((err) => console.log(err));
  }, []);
  return (
    <div>
      <div>
        <Row>
          <Col>
            <h3>Requests</h3>

            <Button
              variant="success"
              onClick={() => showRequestsForm(true, true, false)}
            >
              New Request
            </Button>
          </Col>
        </Row>
      </div>

      {research && expense && purchase && reallocation ? (
        <RequestsTable
          researchData={research}
          expenseData={expense}
          purchaseData={purchase}
          reallocationData={reallocation}
        />
      ) : (
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      )}
    </div>
  );
};

export default RequestsPage;
