import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";
import { LinkContainer } from "react-router-bootstrap";

const Header = ({
  user,
  userType,
  setModalState,
  handleLogout,
  setModalState2,
}) => {
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" bg="info" variant="dark">
        <LinkContainer to="/">
          <Navbar.Brand> Grant App</Navbar.Brand>
        </LinkContainer>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          {user ? (
            <>
              {" "}
              <Nav className="mr-auto">
                <LinkContainer to="/grants">
                  <Nav.Link>Grants</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/requests">
                  <Nav.Link> Requests</Nav.Link>
                </LinkContainer>

                {userType === "admin" ? (
                  <LinkContainer to="/dashboard">
                    <Nav.Link> Dashboard</Nav.Link>
                  </LinkContainer>
                ) : null}

                {userType === "admin" ? (
                  <LinkContainer to="/report">
                    <Nav.Link> Reports</Nav.Link>
                  </LinkContainer>
                ) : null}

                <NavDropdown title="Create" id="basic-nav-dropdown">
                  {userType === "user" ? (
                    <NavDropdown.Item
                      onClick={() => setModalState2(true, true, false, false)}
                    >
                      New Request
                    </NavDropdown.Item>
                  ) : null}

                  {userType === "admin" ? (
                    <>
                      <NavDropdown.Item
                        onClick={() => setModalState2(true, false, true, false)}
                      >
                        New Grant
                      </NavDropdown.Item>

                      <NavDropdown.Item
                        onClick={() => setModalState2(true, false, false, true)}
                      >
                        New Admin
                      </NavDropdown.Item>
                    </>
                  ) : null}
                </NavDropdown>
              </Nav>
              {
                <Nav>
                  <Navbar.Brand>Welcome {user}</Navbar.Brand>

                  <NavDropdown title="Settings" id="basic-nav-dropdown">
                    <NavDropdown.Item href="/profile">Profile</NavDropdown.Item>
                    {userType === "admin" ? (
                      <NavDropdown.Item href="/adminslist">
                        Admin List
                      </NavDropdown.Item>
                    ) : null}
                    <NavDropdown.Item href="/logout" onClick={handleLogout}>
                      Logout
                    </NavDropdown.Item>
                  </NavDropdown>
                </Nav>
              }
            </>
          ) : (
            <>
              <Nav>
                <Nav.Link onClick={() => setModalState(true, true, false)}>
                  Login
                </Nav.Link>
                <Nav.Link onClick={() => setModalState(true, false, true)}>
                  Register
                </Nav.Link>
              </Nav>
            </>
          )}
        </Navbar.Collapse>
      </Navbar>
    </div>
  );
};

export default Header;
