import React, { useState } from "react";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import Nav from "react-bootstrap/Nav";
import Form from "react-bootstrap/Form";
import { LinkContainer } from "react-router-bootstrap";

const GrantsTable = ({
  data,
  removeGrant,
  addToDeleteList,
  grants,
  sortGrants,
  deleteMany,
}) => {
  const orderingState = ["none", "asc", "desc"];
  const [costCenterOrderIndex, setCostCenterOrderIndex] = useState(0);
  const [titleOrderIndex, setTitleOrderIndex] = useState(0);
  const [piOrderIndex, setPiOrderIndex] = useState(0);
  const [typeOrderIndex, setTypeOrderIndex] = useState(0);
  const [collegeOrderIndex, setCollegeOrderIndex] = useState(0);
  const [yearOrderIndex, setYearOrderIndex] = useState(0);
  const [statusOrderIndex, setStatusOrderIndex] = useState(0);

  const setOrdering = (field, fieldIndex, setfieldIndex) => {
    let newIndex;
    if (fieldIndex === 2) {
      newIndex = 0;
      setfieldIndex(newIndex);
    } else {
      newIndex = fieldIndex + 1;
      setfieldIndex(newIndex);
    }
    sortGrants(field, orderingState[newIndex]);
  };
  const renderDeleteItem = (grant) => {
    return deleteMany ? (
      <Form.Group controlId="formBasicCheckbox">
        <Form.Check
          type="checkbox"
          label="Delete"
          onClick={() => {
            addToDeleteList(grant._id);
          }}
        />
      </Form.Group>
    ) : (
      <Button
        variant="danger"
        size="sm"
        className="deleteButton"
        onClick={() => removeGrant(grant._id)}
      >
        Delete
      </Button>
    );
  };
  // .sort((a, b) => parseInt(a.costCenter) - parseInt(b.costCenter))
  return (
    <div>
      <Table striped bordered hover responsive>
        <thead>
          <tr>
            <th className="sort-by-parent" id="cc">
              <span
                className="sort-by"
                onClick={() =>
                  setOrdering(
                    "costCenter",
                    costCenterOrderIndex,
                    setCostCenterOrderIndex
                  )
                }
              ></span>
              Cost Center
            </th>
            <th className="sort-by-parent">
              <span
                className="sort-by"
                onClick={() =>
                  setOrdering("title", titleOrderIndex, setTitleOrderIndex)
                }
              ></span>
              Title
            </th>
            <th className="sort-by-parent">
              <span
                className="sort-by"
                onClick={() => setOrdering("pi", piOrderIndex, setPiOrderIndex)}
              ></span>
              PI
            </th>
            <th className="sort-by-parent">
              <span
                className="sort-by"
                onClick={() =>
                  setOrdering("grantType", typeOrderIndex, setTypeOrderIndex)
                }
              ></span>
              Type
            </th>
            <th className="sort-by-parent">
              <span
                className="sort-by"
                onClick={() =>
                  setOrdering(
                    "college",
                    collegeOrderIndex,
                    setCollegeOrderIndex
                  )
                }
              ></span>
              College
            </th>
            <th className="sort-by-parent">
              <span
                className="sort-by"
                onClick={() =>
                  setOrdering("year", yearOrderIndex, setYearOrderIndex)
                }
              ></span>
              Year
            </th>
            <th className="sort-by-parent">
              <span
                className="sort-by"
                onClick={() =>
                  setOrdering("status", statusOrderIndex, setStatusOrderIndex)
                }
              ></span>
              Status
            </th>
            <th>Date Created</th>
          </tr>
        </thead>
        <tbody>
          {data.map((grant) =>
            grants.indexOf(grant) !== -1 ? (
              <tr key={grant._id}>
                <td className="table-info">{grant.costCenter}</td>

                <td className="table-success">
                  <LinkContainer to={`/grants/${grant._id}`}>
                    <Nav.Link>{grant.title}</Nav.Link>
                  </LinkContainer>
                </td>

                <td className="table-warning">{grant.pi.name}</td>
                <td className="table-secondary">{grant.grantType}</td>
                <td>{grant.college}</td>
                <td className="table-primary">{grant.year}</td>
                <td className="table-danger">{grant.status}</td>
                <td className="table-success">
                  {new Date(grant.dateCreated).toDateString()}
                </td>
                {JSON.parse(window.localStorage.getItem("loggedInUser"))
                  .userType === "admin" ? (
                  <td> {renderDeleteItem(grant)}</td>
                ) : null}
              </tr>
            ) : null
          )}
        </tbody>
      </Table>
    </div>
  );
};

export default GrantsTable;
