import React from "react";
import {Table} from "react-bootstrap";
import { v4 as uuidv4 } from "uuid";
const GrantExpenses = ({ expenses }) => {
  return (
    <div>
      <Table striped bordered hover responsive>
        <thead>
          <tr>
            <th>Expense</th>
            <th>Amount</th>
          </tr>
        </thead>
        <tbody>
          {expenses.map((expense) => (
            <tr key={uuidv4()}>
              <td className="table-primary">{expense.details}</td>
              <td>{expense.amount}</td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
};

export default GrantExpenses;
