import React, { useState } from "react";
import useField from "../../hooks/useField";
import Alert from "react-bootstrap/Alert";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";

import userService from "../../services/userService";

const AdminForm = () => {
  const email = useField("email");
  const password = useField("password");
  const name = useField("text");
  const [notification, setNotification] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  const validateForm = () => {
    let errorStr = "";
    const checkEmailFormat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(
      email.value
    );
    if (!checkEmailFormat && email.value) {
      errorStr += " Email not in valid format.\n";
    }
    if (email.value.length < 5 || !email.value) {
      errorStr += " Email must be at least 5 characters long.\n";
    }

    if (name.value.length < 5 || !name.value) {
      errorStr += " Name must be at least 5 characters long.\n";
    }

    if (password.value.length < 6 || !password.value) {
      errorStr += " Password should be at least 6 characters long.\n";
    }
    if (errorStr) {
      return { validated: false, errorStr };
    } else {
      return { validated: true, errorStr };
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    const { validated, errorStr } = validateForm();
    if (!validated) {
      event.stopPropagation();
      displayError(errorStr);
      return;
    }
    userService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );

    userService
      .createUser(name.value, email.value, password.value, "admin")
      .then((newUser) => {
        console.log(newUser);
        setNotification(true);
        displayError("New Admin Created: " + newUser.name);
      })
      .catch((err) => {
        displayError(err.response.data.error);
        console.log(err.response);
      });
  };

  const displayError = (err) => {
    console.log(err);
    setErrorMessage(err);
    setNotification(true);
    setTimeout(() => {
      setNotification(false);
    }, 5000);
  };

  return (
    <div>
      <Form>
        <Row>
          <Col>
            {notification ? (
              <Alert variant={"danger"}>{errorMessage}</Alert>
            ) : null}
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group controlId="formBasicName">
              <Form.Label> Name</Form.Label>
              <Form.Control {...name} placeholder="Enter Name" />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control {...email} placeholder="Enter email" />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control {...password} placeholder="Enter password" />
            </Form.Group>
          </Col>
        </Row>
        <Button variant="success" type="submit" onClick={handleSubmit}>
          Create New Admin
        </Button>
      </Form>
    </div>
  );
};

export default AdminForm;
