import React, { useState, useEffect } from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

const Comment = ({ comment, updateComment, deleteComment }) => {
  const [editing, setEditing] = useState(false);
  const [updatedComment, setUpdatedComment] = useState("");
  const [border, setBorder] = useState("");
  useEffect(() => {
    if (comment.isNew) {
      setBorder("success");
      setTimeout(() => {
        setBorder("");
        comment.isNew = false;
      }, 3500);
    }
  }, []);
  const setValue = (event) => {
    setUpdatedComment(event.target.value);
  };

  const onSubmit = async () => {
    updateComment(updatedComment, comment._id);
    setUpdatedComment("");
    comment.text = updatedComment;
    setEditing(false);
    setBorder("success");
    setTimeout(() => setBorder("none"), 3500);
  };

  const deleteButtonHandler = () => {
    let confirm = window.confirm(
      "Are you sure you want to delete this comment?"
    );
    if (confirm) {
      deleteComment(comment._id);
    }
  };

  return (
    <div>
      <Card border={border}>
        <Card.Header>
          {new Date(comment.date).toDateString()}{" "}
          {new Date(comment.date).toTimeString().split(" ")[0]}
          {comment.edited ? " edited" : null}
        </Card.Header>
        <Card.Body>
          <Card.Title>{comment.user.name}</Card.Title>
          {!editing ? (
            <Card.Text>{comment.text}</Card.Text>
          ) : (
            <Form>
              <Form.Group controlId="exampleForm.ControlTextarea1">
                <Form.Control
                  as="textarea"
                  rows="3"
                  value={updatedComment}
                  onChange={setValue}
                />
              </Form.Group>
              <Button variant="primary" onClick={onSubmit}>
                Update Comment
              </Button>
            </Form>
          )}
          <br />
          {JSON.parse(window.localStorage.getItem("loggedInUser")).name ===
          comment.user.name ? (
            <Button
              variant="info"
              onClick={() => {
                setEditing(!editing);
                setUpdatedComment(comment.text);
              }}
            >
              {!editing ? "Edit" : "Cancel"}
            </Button>
          ) : null}{" "}
          {JSON.parse(window.localStorage.getItem("loggedInUser")).name ===
            comment.user.name ||
          JSON.parse(window.localStorage.getItem("loggedInUser")).userType ===
            "admin" ? (
            <Button variant="danger" onClick={deleteButtonHandler}>
              Delete
            </Button>
          ) : null}
        </Card.Body>
      </Card>
    </div>
  );
};

export default Comment;
