import React, { useState, useEffect } from "react";
import useField from "../../hooks/useField";
import FilterForm from "./FilterForm";
import GrantsTable from "./GrantsTable";
import Pagination from "react-bootstrap/Pagination";
import Spinner from "react-bootstrap/Spinner";
import Button from "react-bootstrap/Button";
import grantService from "../../services/grantService";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

const GrantsPage = ({ showGrantsForm, addNotification }) => {
  const [grants, setGrants] = useState([]);
  const [arraySlice, setArraySlice] = useState([]);
  const [activeSlice, setActiveSlice] = useState(0);
  const [reqMade, setReqMade] = useState(false);

  const titleFilter = useField();
  const yearFilter = useField();
  const piFilter = useField();
  const statusFilter = useField();
  const typeFilter = useField();
  const costCenterFilter = useField();
  const collegeFilter = useField();

  const [costCenterAsc, setCostCenterAsc] = useState(false);
  const [costCenterDesc, setCostCenterDesc] = useState(false);
  const [titleAsc, setTitleAsc] = useState(false);
  const [titleDesc, setTitleDesc] = useState(false);
  const [piAsc, setPiAsc] = useState(false);
  const [piDesc, setPiDesc] = useState(false);
  const [grantTypeAsc, setGrantTypeAsc] = useState(false);
  const [grantTypeDesc, setGrantTypeDesc] = useState(false);
  const [collegeAsc, setCollegeAsc] = useState(false);
  const [collegeDesc, setCollegeDesc] = useState(false);
  const [yearAsc, setYearAsc] = useState(false);
  const [yearDesc, setYearDesc] = useState(false);
  const [statusAsc, setStatusAsc] = useState(false);
  const [statusDesc, setStatusDesc] = useState(false);

  const [deleteMany, setDeleteMany] = useState(false);
  const [selectDelete, setSelectDelete] = useState([]);

  const addToDeleteList = (id) => {
    setSelectDelete([...selectDelete, id]);
  };

  const sortGrants = (field, order) => {
    //alert(field + " " + order);
    setCostCenterAsc(false);
    setCostCenterDesc(false);
    setTitleAsc(false);
    setTitleDesc(false);
    setPiAsc(false);
    setPiDesc(false);
    setGrantTypeAsc(false);
    setGrantTypeDesc(false);
    setCollegeAsc(false);
    setCollegeDesc(false);
    setYearAsc(false);
    setYearDesc(false);
    setStatusAsc(false);
    setStatusDesc(false);
    const setOrder = (setAscState, setDescState) => {
      if (order === "asc") {
        setAscState(true);
        setDescState(false);
      } else if (order === "desc") {
        setAscState(false);
        setDescState(true);
      } else if (order === "none") {
        setAscState(false);
        setDescState(false);
      }
      renderGrants();
    };
    switch (field) {
      case "costCenter":
        setOrder(setCostCenterAsc, setCostCenterDesc);
        break;
      case "title":
        setOrder(setTitleAsc, setTitleDesc);
        break;
      case "pi":
        setOrder(setPiAsc, setPiDesc);
        break;
      case "grantType":
        setOrder(setGrantTypeAsc, setGrantTypeDesc);
        break;
      case "college":
        setOrder(setCollegeAsc, setCollegeDesc);
        break;
      case "year":
        setOrder(setYearAsc, setYearDesc);
        break;
      case "status":
        setOrder(setStatusAsc, setStatusDesc);
        break;
    }
  };

  const clearFilters = (event) => {
    event.preventDefault();
    titleFilter.setValue("");
    piFilter.setValue("");
    statusFilter.setValue("");
    yearFilter.setValue("");
    typeFilter.setValue("");
    collegeFilter.setValue("");
    costCenterFilter.setValue("");
    setCostCenterAsc(false);
    setCostCenterDesc(false);
    setTitleAsc(false);
    setTitleDesc(false);
    setPiAsc(false);
    setPiDesc(false);
    setGrantTypeAsc(false);
    setGrantTypeDesc(false);
    setCollegeAsc(false);
    setCollegeDesc(false);
    setYearAsc(false);
    setYearDesc(false);
    setStatusAsc(false);
    setStatusDesc(false);

    setActiveSlice(0);
  };

  useEffect(() => {
    grantService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    const userName = JSON.parse(window.localStorage.getItem("loggedInUser"))
      .name;
    console.log(userName);
    grantService
      .getAll()
      .then((data) => {
        setGrants(data);
        const tempGrants = [...data];
        const newArray = [];
        while (tempGrants.length) {
          newArray.push(tempGrants.splice(0, 10));
        }
        setArraySlice(newArray);
        const pageQuery = window.location.search
          .substring(1)
          .split("&")[0]
          .split("=")[0];
        const pageQueryValue = window.location.search
          .substring(1)
          .split("&")[0]
          .split("=")[1];

        if (pageQuery === "page") {
          try {
            console.log(activeSlice);
            setActiveSlice(Number(pageQueryValue) - 1);
          } catch (err) {
            window.location = "/grants";
          }
        }
      })
      .then(() => setReqMade(true));
  }, []);

  const removeGrant = (id) => {
    const confirm = window.confirm("Are you sure?");

    if (confirm) {
      grantService
        .deleteGrant(id)
        .then((response) => {
          if (response === 204) {
            addNotification(
              "Deleted " + grants.find((grant) => grant._id === id).title
            );
            setGrants(grants.filter((grant) => grant._id !== id));
            console.log(grants);
            const tempGrants = [...grants];
            const newArray = [];
            while (tempGrants.length) {
              newArray.push(tempGrants.splice(0, 10));
            }
            setArraySlice(newArray);
            if (!arraySlice[activeSlice]) {
              setActiveSlice(activeSlice - 1);
            }
          }

          console.log("deleted");
        })
        .catch((err) => console.log(err));
    }
  };

  const removeGrants = async () => {
    const confirm = window.confirm("Are you sure?");
    const grantsObject = { ids: selectDelete };
    console.log(grantsObject);
    if (confirm) {
      await grantService.setToken(
        JSON.parse(window.localStorage.getItem("loggedInUser")).token
      );
      grantService

        .deleteGrants(grantsObject)
        .then((response) => {
          if (response === 204) {
            grantsObject.ids.map((id) =>
              setGrants(grants.filter((grant) => grant._id !== id))
            );
            const tempGrants = [...grants];
            const newArray = [];
            while (tempGrants.length) {
              newArray.push(tempGrants.splice(0, 10));
            }
            setArraySlice(newArray);
            if (!arraySlice[activeSlice]) {
              setActiveSlice(activeSlice - 1);
            }
          }
          addNotification("Deleted Multiple Grants!");
          console.log("deleted many");
          setSelectDelete([]);
          setDeleteMany(false);
        })
        .catch((err) => console.log(err.response));
    }
  };

  const filterPresent =
    titleFilter.value ||
    piFilter.value ||
    statusFilter.value ||
    yearFilter.value ||
    typeFilter.value ||
    costCenterFilter.value ||
    collegeFilter.value;

  const renderPagination = () => {
    let items = [];
    for (let number = 1; number <= arraySlice.length; number++) {
      items.push(
        <Pagination.Item
          key={number}
          active={number - 1 === activeSlice}
          onClick={() => {
            setActiveSlice(number - 1);
            window.history.pushState("", "", `/grants?page=${number}`);
            setCostCenterAsc(false);
            setCostCenterDesc(false);
            setTitleAsc(false);
            setTitleDesc(false);
            setPiAsc(false);
            setPiDesc(false);
            setGrantTypeAsc(false);
            setGrantTypeDesc(false);
            setCollegeAsc(false);
            setCollegeDesc(false);
            setYearAsc(false);
            setYearDesc(false);
            setStatusAsc(false);
            setStatusDesc(false);
          }}
        >
          {number}
        </Pagination.Item>
      );
    }
    return items;
  };

  const filteredSlice = (arr) => {
    const filtered = arr.filter(
      (grant) =>
        grant.title.toLowerCase().includes(titleFilter.value.toLowerCase()) &&
        grant.pi.name.toLowerCase().includes(piFilter.value.toLowerCase()) &&
        grant.year.toString().includes(yearFilter.value) &&
        grant.grantType.includes(typeFilter.value) &&
        grant.status.toLowerCase().includes(statusFilter.value.toLowerCase()) &&
        grant.college.includes(collegeFilter.value) &&
        grant.costCenter.toString().includes(costCenterFilter.value)
    );
    return filtered;
  };

  const renderGrants = () => {
    let ggs = filterPresent ? grants : arraySlice[activeSlice];
    ggs
      .sort((a, b) => {
        if (costCenterAsc) {
          return parseInt(a.costCenter) - parseInt(b.costCenter);
        } else if (costCenterDesc) {
          return parseInt(b.costCenter) - parseInt(a.costCenter);
        } else {
          return 0;
        }
      })
      .sort((a, b) => {
        if (titleAsc) {
          return a["title"].localeCompare(b["title"]);
        } else if (titleDesc) {
          return b["title"].localeCompare(a["title"]);
        } else {
          return 0;
        }
      })
      .sort((a, b) => {
        if (piAsc) {
          return a["pi"].toLowerCase().localeCompare(b["pi"].toLowerCase());
        } else if (piDesc) {
          return b["pi"].toLowerCase().localeCompare(a["pi"].toLowerCase());
        } else {
          return 0;
        }
      })
      .sort((a, b) => {
        if (grantTypeAsc) {
          return a["grantType"].localeCompare(b["grantType"]);
        } else if (grantTypeDesc) {
          return b["grantType"].localeCompare(a["grantType"]);
        } else {
          return 0;
        }
      })
      .sort((a, b) => {
        if (collegeAsc) {
          return a["college"].localeCompare(b["college"]);
        } else if (collegeDesc) {
          return b["college"].localeCompare(a["college"]);
        } else {
          return 0;
        }
      })
      .sort((a, b) => {
        const aYear = parseInt(a.year.split("/")[0]);
        const bYear = parseInt(b.year.split("/")[0]);

        if (yearAsc) {
          return aYear - bYear;
        } else if (yearDesc) {
          return bYear - aYear;
        } else {
          return aYear === bYear;
        }
      })
      .sort((a, b) => {
        if (statusAsc) {
          return a["status"].localeCompare(b["status"]);
        } else if (statusDesc) {
          return b["college"].localeCompare(a["college"]);
        } else {
          return 0;
        }
      });
  };

  return (
    <div>
      <FilterForm
        titleChange={titleFilter}
        piChange={piFilter}
        yearChange={yearFilter}
        statusChange={statusFilter}
        typeChange={typeFilter}
        costCenterChange={costCenterFilter}
        collegeChange={collegeFilter}
        clearFilters={clearFilters}
      />
      {JSON.parse(window.localStorage.getItem("loggedInUser")).userType ===
      "admin" ? (
        <Button
          variant="success"
          onClick={() => showGrantsForm(true, false, true)}
        >
          New Grant
        </Button>
      ) : null}
      {JSON.parse(window.localStorage.getItem("loggedInUser")).userType ===
      "admin" ? (
        <Button
          variant="danger"
          onClick={() =>
            !deleteMany ? setDeleteMany(!deleteMany) : removeGrants()
          }
        >
          {!deleteMany ? "Delete Many" : "Delete"}
        </Button>
      ) : null}

      {deleteMany ? (
        <Button
          variant="success"
          onClick={() => {
            setDeleteMany(false);
            setSelectDelete([]);
          }}
        >
          Cancel
        </Button>
      ) : null}

      {!filterPresent && arraySlice.length > 1 ? (
        <Row>
          <Col>
            <Pagination>{renderPagination()}</Pagination>
          </Col>
        </Row>
      ) : null}
      {Array.isArray(arraySlice[0]) ? (
        <GrantsTable
          data={
            !filterPresent
              ? filteredSlice(arraySlice[activeSlice])
              : filteredSlice(grants)
          }
          grants={grants}
          removeGrant={removeGrant}
          addToDeleteList={addToDeleteList}
          sortGrants={sortGrants}
          deleteMany={deleteMany}
        />
      ) : !reqMade ? (
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      ) : (
        "No Grants"
      )}
    </div>
  );
};

export default GrantsPage;
