import React, { useState, useEffect } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { CSVLink } from "react-csv";
import ExportCSV from "./ExportCSV";
import ReportTable from "./ReportTable";

import grantService from "../../services/grantService";

const Report = () => {
  const [collegeCount, setCollegeCount] = useState([]);
  const [collegeCountExport, setCollegeCountExport] = useState(false);
  const [collegeCountExcel, setCollegeCountExcel] = useState([]);

  const [typeCount, setTypeCount] = useState([]);
  const [typeCountExport, setTypeCountExport] = useState(false);
  const [typeCountExcel, setTypeCountExcel] = useState([]);

  const [statusCount, setStatusCount] = useState([]);
  const [statusCountExport, setStatusCountExport] = useState(false);
  const [statusCountExcel, setStatusCountExcel] = useState([]);

  const [collegeGrantSpending, setCollegeGrantSpending] = useState([]);
  const [collegeGrantSpendingExport, setCollegeGrantSpendingExport] = useState(
    false
  );
  const [collegeGrantSpendingExcel, setCollegeGrantSpendingExcel] = useState(
    []
  );

  const [typeGrantSpending, setTypeGrantSpending] = useState([]);
  const [typeGrantSpendingExport, setTypeGrantSpendingExport] = useState(false);
  const [typeGrantSpendingExcel, setTypeGrantSpendingExcel] = useState([]);

  const [totalYearSpending, setTotalYearSpending] = useState([]);
  const [totalYearSpendingExport, setTotalYearSpendingExport] = useState(false);
  const [totalYearSpendingExcel, setTotalYearSpendingExcel] = useState([]);

  const [tableData, setTableData] = useState([]);

  const setReportData = (data, field, setField, col1, col2) => {
    let fileData = [];

    let total = 0;

    Object.entries(data).forEach((row, i) => {
      if (i === 0) {
        fileData.push({});
      }

      const rowObject = {};
      rowObject[col1] = row[0];
      rowObject[col2] = row[1];
      fileData.push(rowObject);
      total += row[1];
    });

    fileData.push({});

    const totalObject = {};
    totalObject[col1] = "Total";
    totalObject[col2] = total;
    fileData.push(totalObject);
    fileData.push({});
    fileData.push({});
    setField([...field, ...fileData]);
  };

  useEffect(() => {
    grantService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
  }, []);

  const cceChange = () => {
    setCollegeCountExport(!collegeCountExport);

    if (!collegeCountExport && collegeCount.length === 0) {
      grantService
        .getReport("counter/college")
        .then((collegeData) => {
          setCollegeCount(collegeData);
          setReportData(
            collegeData,
            collegeCount,
            setCollegeCountExcel,
            "College Name",
            "Number of Grants"
          );
        })

        .catch((err) => console.log(err));
    }
  };

  const tceChange = () => {
    setTypeCountExport(!typeCountExport);
    if (!typeCountExport && typeCount.length === 0) {
      grantService
        .getReport("counter/type")
        .then((data) => {
          setTypeCount(data);

          setReportData(
            data,
            typeCount,
            setTypeCountExcel,
            "Grant Type",
            "Number of Grants"
          );
        })
        .catch((err) => console.log(err));
    }
  };

  const sceChange = () => {
    setStatusCountExport(!statusCountExport);
    if (!statusCountExport && statusCount.length === 0) {
      grantService
        .getReport("counter/status")
        .then((data) => {
          setStatusCount(data);
          setReportData(
            data,
            statusCount,
            setStatusCountExcel,
            "Grant Status",
            "Number of Grants"
          );
        })
        .catch((err) => console.log(err));
    }
  };

  const cgsChange = () => {
    setCollegeGrantSpendingExport(!collegeGrantSpendingExport);
    if (!collegeGrantSpendingExport && collegeGrantSpending.length === 0) {
      grantService
        .getReport("counter/college/spending")
        .then((data) => {
          setCollegeGrantSpending(data);

          setReportData(
            data,
            collegeGrantSpending,
            setCollegeGrantSpendingExcel,
            "College Name",
            "Spending"
          );
        })
        .catch((err) => console.log(err));
    }
  };

  const tgsChange = () => {
    setTypeGrantSpendingExport(!typeGrantSpendingExport);
    if (!typeGrantSpendingExport && typeGrantSpending.length === 0) {
      grantService
        .getReport("counter/type/spending")
        .then((data) => {
          setTypeGrantSpending(data);
          setReportData(
            data,
            typeGrantSpending,
            setTypeGrantSpendingExcel,
            "Grant Type",
            "Spending"
          );
        })
        .catch((err) => console.log(err));
    }
  };

  const tysChange = () => {
    setTotalYearSpendingExport(!totalYearSpendingExport);
    if (!totalYearSpendingExport && totalYearSpending.length === 0) {
      grantService
        .getReport("counter/yearlyspending")
        .then((data) => {
          setTotalYearSpending(data);

          setReportData(
            data,
            totalYearSpending,
            setTotalYearSpendingExcel,
            "Year",
            "Spending"
          );
        })
        .catch((err) => console.log(err));
    }
  };

  return (
    <div>
      <h1>Reports</h1>

      <Form as={Col}>
        <div key={`default-checkbox`} className="mb-3">
          <Form.Group>
            <Row>
              <Col md="auto">
                <Form.Check
                  type={"checkbox"}
                  label="Total Count of grants by college"
                  value={collegeCountExport.value}
                  onChange={cceChange}
                />
              </Col>

              {collegeCount.length !== 0 ? (
                <Col md="auto">
                  <Button
                    variant="primary"
                    size="sm"
                    onClick={() => setTableData(collegeCount)}
                  >
                    View Data
                  </Button>
                </Col>
              ) : null}
              {collegeCountExcel && collegeCountExport ? (
                <Col md="auto">
                  <ExportCSV
                    csvData={[...collegeCountExcel]}
                    fileName={"College-Count"}
                  />
                </Col>
              ) : null}
            </Row>
          </Form.Group>

          <Form.Group>
            <Row>
              <Col md="auto">
                <Form.Check
                  type={"checkbox"}
                  label="Total count of grants by type"
                  value={typeCountExport.value}
                  onChange={tceChange}
                />
              </Col>

              {typeCount.length !== 0 ? (
                <Col md="auto">
                  <Button
                    variant="primary"
                    size="sm"
                    onClick={() => setTableData(typeCount)}
                  >
                    View Data
                  </Button>
                </Col>
              ) : null}

              {typeCountExcel && typeCountExport ? (
                <Col md="auto">
                  <ExportCSV
                    csvData={[...typeCountExcel]}
                    fileName={"Grant-Type-Count"}
                  />
                </Col>
              ) : null}
            </Row>
          </Form.Group>

          <Form.Group>
            <Row>
              <Col md="auto">
                <Form.Check
                  type={"checkbox"}
                  label="Total count of grants by status"
                  value={statusCountExport.value}
                  onChange={sceChange}
                />
              </Col>
              {statusCount.length !== 0 ? (
                <Col md="auto">
                  <Button
                    variant="primary"
                    size="sm"
                    onClick={() => setTableData(statusCount)}
                  >
                    View Data
                  </Button>
                </Col>
              ) : null}

              {statusCountExcel && statusCountExport ? (
                <Col md="auto">
                  <ExportCSV
                    csvData={[...statusCountExcel]}
                    fileName={"Grant-Status-Count"}
                  />
                </Col>
              ) : null}
            </Row>
          </Form.Group>

          <Form.Group>
            <Row>
              <Col md="auto">
                <Form.Check
                  type="checkbox"
                  label="Total spending of grants for each college"
                  value={collegeGrantSpendingExport.value}
                  onChange={cgsChange}
                />
              </Col>
              {collegeGrantSpending.length !== 0 ? (
                <Col md="auto">
                  <Button
                    variant="primary"
                    size="sm"
                    onClick={() => setTableData(collegeGrantSpending)}
                  >
                    View Data
                  </Button>
                </Col>
              ) : null}

              {collegeGrantSpendingExcel && collegeGrantSpendingExport ? (
                <Col md="auto">
                  <ExportCSV
                    csvData={[...collegeGrantSpendingExcel]}
                    fileName={"Grant-College-Spending"}
                  />
                </Col>
              ) : null}
            </Row>
          </Form.Group>
          <Form.Group>
            <Row>
              <Col md="auto">
                <Form.Check
                  type={"checkbox"}
                  label="Total spending of grants for each type"
                  value={typeGrantSpendingExport.value}
                  onChange={tgsChange}
                />
              </Col>
              {typeGrantSpending.length !== 0 ? (
                <Col md="auto">
                  <Button
                    variant="primary"
                    size="sm"
                    onClick={() => setTableData(typeGrantSpending)}
                  >
                    View Data
                  </Button>
                </Col>
              ) : null}
              {typeGrantSpendingExcel && typeGrantSpendingExport ? (
                <Col md="auto">
                  <ExportCSV
                    csvData={[...typeGrantSpendingExcel]}
                    fileName={"Grant-Type-Spending"}
                  />
                </Col>
              ) : null}
            </Row>
          </Form.Group>

          <Form.Group>
            <Row>
              <Col md="auto">
                <Form.Check
                  type={"checkbox"}
                  label="Total grant spending for all years"
                  value={totalYearSpendingExport.value}
                  onChange={tysChange}
                />
              </Col>
              {totalYearSpending.length !== 0 ? (
                <Col md="auto">
                  <Button
                    variant="primary"
                    size="sm"
                    onClick={() => setTableData(totalYearSpending)}
                  >
                    View Data
                  </Button>
                </Col>
              ) : null}
              {totalYearSpendingExcel && totalYearSpendingExport ? (
                <Col md="auto">
                  <ExportCSV
                    csvData={[...totalYearSpendingExcel]}
                    fileName={"Total-Grant-Year-Spending"}
                  />
                </Col>
              ) : null}
            </Row>
          </Form.Group>
        </div>
      </Form>
      {tableData.length !== 0 ? <ReportTable data={tableData} /> : null}
    </div>
  );
};

export default Report;
