import React, { useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";

import Header from "./components/Header";
import FormModal from "./components/FormModal";
import LoginForm from "./components/Forms/LoginForm";
import ImageSlider from "./components/ImageSlider";
import Grant from "./components/Grant/Grant";
import GrantsPage from "./components/GrantsPage/GrantsPage";
import NotFound from "./components/NotFound";
import RequestForm from "./components/Forms/RequestForm";
import RequestsPage from "./components/RequestsPage/RequestsPage";
import GrantsForm from "./components/Forms/GrantsForm";
import AdminForm from "./components/Forms/AdminForm";
import Request from "./components/Request/Request";
import Dashboard from "./components/Dashboard";
import ProfilePage from "./components/ProfilePage/ProfilePage";
import AdminsList from "./components/AdminsList";
import Report from "./components/Report/Report";

import ReactNotification from "react-notifications-component";
import "react-notifications-component/dist/theme.css";
import { store } from "react-notifications-component";

import grantService from "./services/grantService";
import requestService from "./services/requestService";

const App = () => {
  const [useRegisterForm, setUseRegisterForm] = useState(false);
  const [useLoginForm, setUseLoginForm] = useState(false);
  const [showModal, setShowModal] = useState(false);

  const [showCreateModal, setShowCreateModal] = useState(false);
  const [useRequestForm, setUseRequestForm] = useState(false);
  const [useGrantForm, setUseGrantForm] = useState(false);
  const [useAdminForm, setUseAdminForm] = useState(false);

  const [user, setUser] = useState(null);

  useEffect(() => {
    const loggedUserJSON = JSON.parse(
      window.localStorage.getItem("loggedInUser")
    );

    if (loggedUserJSON) {
      setUser(loggedUserJSON);
      grantService.setToken(loggedUserJSON.token);
      requestService.setToken(loggedUserJSON.token);
    }
  }, []);

  const getTokenFromBrowser = () => {
    const loggedUserJSON = JSON.parse(
      window.localStorage.getItem("loggedInUser")
    );

    return loggedUserJSON ? loggedUserJSON.token : false;
  };

  const checkUserType = (type) => {
    const loggedUserJSON = JSON.parse(
      window.localStorage.getItem("loggedInUser")
    );
    return loggedUserJSON.userType === type;
  };

  const setModalState = (modalValue, loginValue, regValue) => {
    setShowModal(modalValue);
    setUseLoginForm(loginValue);
    setUseRegisterForm(regValue);
  };

  const setModalState2 = (modalValue, reqValue, createValue, adminValue) => {
    setShowCreateModal(modalValue);
    setUseRequestForm(reqValue);
    setUseGrantForm(createValue);
    setUseAdminForm(adminValue);
  };

  const handleLogout = () => {
    window.localStorage.clear();
    setUser(null);
  };

  const renderModal = () => {
    if (useRequestForm) {
      return <RequestForm setModalState2={setModalState2} />;
    } else if (useGrantForm) {
      return <GrantsForm setModalState2={setModalState2} />;
    } else if (useAdminForm) {
      return <AdminForm setModalState2={setModalState2} />;
    } else {
      return null;
    }
  };
  const renderModalTitle = () => {
    if (useRequestForm) {
      return "Create New Request";
    } else if (useGrantForm) {
      return "Create New Grant";
    } else if (useAdminForm) {
      return "Create New Admin";
    }
  };

  const addNotification = (title) => {
    store.addNotification({
      title,
      message: "Success!",
      type: "success",
      insert: "top",
      container: "bottom-left",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 5000,
        onScreen: true,
      },
    });
  };

  return (
    <Router basename="/">
      <div className="app-container">
        <ReactNotification />
        <Header
          user={user ? user.name : ""}
          userType={user ? user.userType : ""}
          setModalState={setModalState}
          setModalState2={setModalState2}
          handleLogout={handleLogout}
        />

        <FormModal
          useRegisterForm={useRegisterForm}
          onHide={() => {
            setModalState(false, false, false);
            window.history.back();
          }}
          show={showModal}
          title={useRegisterForm ? "Register" : "Login"}
        >
          <LoginForm
            useRegisterForm={useRegisterForm}
            useLoginForm={useLoginForm}
            setUser={setUser}
            setShowModal={setShowModal}
          />
        </FormModal>

        <FormModal
          useRequestForm={useRequestForm}
          onHide={() => {
            setModalState2(false, false, false, false);
          }}
          show={showCreateModal}
          title={renderModalTitle()}
        >
          {renderModal()}
        </FormModal>
        <Switch>
          <Route exact path="/dashboard">
            {getTokenFromBrowser() && checkUserType("admin") ? (
              <Dashboard />
            ) : (
              <NotFound />
            )}
          </Route>

          <Route exact path="/report">
            {getTokenFromBrowser() && checkUserType("admin") ? (
              <Report />
            ) : (
              <NotFound />
            )}
          </Route>

          <Route exact path="/profile">
            {getTokenFromBrowser() ? (
              <ProfilePage user={user} addNotification={addNotification} />
            ) : (
              <NotFound />
            )}
          </Route>

          <Route exact path="/adminslist">
            {getTokenFromBrowser() && checkUserType("admin") ? (
              <AdminsList />
            ) : (
              <NotFound />
            )}
          </Route>

          <Route exact path="/grants/:id">
            {getTokenFromBrowser() ? <Grant /> : <NotFound />}
          </Route>

          <Route path="/grants">
            {getTokenFromBrowser() ? (
              <GrantsPage
                showGrantsForm={setModalState2}
                addNotification={addNotification}
              />
            ) : (
              <NotFound />
            )}
          </Route>

          <Route exact path="/requests/:type/:id">
            {getTokenFromBrowser() ? (
              <Request addNotification={addNotification} />
            ) : (
              <NotFound />
            )}
          </Route>
          <Route path="/requests">
            {getTokenFromBrowser() ? (
              <RequestsPage showRequestsForm={setModalState2} />
            ) : (
              <NotFound />
            )}
          </Route>

          <Route path="/logout">
            <Redirect to="/" />
          </Route>
          <Route exact path="/">
            <ImageSlider />
          </Route>
          <Route path="*">
            <NotFound />
          </Route>
        </Switch>
      </div>
    </Router>
  );
};

export default App;
