import axios from "axios";
const baseUsersUrl = "/api/users";
const baseAdminsUrl = "/api/admins";
let token = null;
let config = {
  headers: { Authorization: token },
};
//http://localhost:3001

const createUser = async (name, email, password, type) => {
  const baseUrl = type === "user" ? baseUsersUrl : baseAdminsUrl;
  const response = await axios.post(baseUrl, { name, email, password }, config);
  return response.data;
};

const getUser = async (type, id) => {
  const baseUrl = type === "user" ? baseUsersUrl : baseAdminsUrl;
  const response = await axios.get(`${baseUrl}/${id}`, config);
  return response.data;
};

const updateUser = async (type, id, newUser) => {
  const baseUrl = type === "user" ? baseUsersUrl : baseAdminsUrl;
  const response = await axios.put(`${baseUrl}/${id}`, newUser, config);
  return response.data;
};

const getAll = async (type) => {
  const baseUrl = type === "user" ? baseUsersUrl : baseAdminsUrl;
  const response = await axios.get(`${baseUrl}`, config);
  return response.data;
};

const setToken = (newToken) => {
  token = `bearer ${newToken}`;
  config.headers.Authorization = token;
};

const getByName = async (nameQuery) => {
  const config = {
    headers: { Authorization: token },
  };
  const response = await axios.get(
    `${baseUsersUrl}/findbyname/${nameQuery}`,
    config
  );
  return response.data;
};

export default {
  createUser,
  setToken,
  getUser,
  updateUser,
  getAll,
  getByName,
};
