import { useState } from "react";

const useField = (type) => {
  const [value, setValue] = useState("");

  const onChange = (event) => {
    setValue(event.target.value);
  };
  if (type) {
    return {
      value,
      onChange,
      type,
      setValue,
    };
  } else {
    return {
      setValue,
      value,
      onChange,
    };
  }
};

export default useField;
