import React from "react";
import { Tab, Row, Col, Nav, Table } from "react-bootstrap";
const GrantRequests = ({ grant }) => {
  const formatNumber = (num) =>
    num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  return (
    <div>
      <Tab.Container id="left-tabs-example" defaultActiveKey="first">
        <Row>
          <Col sm={3}>
            <Nav variant="pills" className="flex-column">
              <Nav.Item>
                <Nav.Link variant="danger" eventKey="first">
                  RA Requests
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="second">Purchase Requests</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="third">Expense Requests</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="fourth">Reallocation Requests</Nav.Link>
              </Nav.Item>
            </Nav>
          </Col>
          <Col sm={9}>
            <Tab.Content>
              <Tab.Pane eventKey="first">
                <>
                  <h3>RA Requests</h3>
                  <Table striped bordered hover responsive>
                    <thead>
                      <tr>
                        <th>Name</th>
                        <th>Salary</th>
                        <th>Status</th>
                      </tr>
                    </thead>
                    <tbody>
                      {grant.rARequests.map((request) => (
                        <tr className="table-primary">
                          <td>
                            <a href={`/requests/research/${request._id}`}>
                              {request.name}
                            </a>
                          </td>
                          <td>{formatNumber(request.salary)}</td>
                          <td>{request.status}</td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </>
              </Tab.Pane>
              <Tab.Pane eventKey="second">
                <h3>Purchase Requests</h3>
                <Table striped bordered hover responsive>
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Quantity</th>
                      <th>Price</th>
                      <th>Vendor</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {grant.purchaseRequests.map((request) => (
                      <tr>
                        <td>
                          <a href={`/requests/purchase/${request._id}`}>
                            {request.name}
                          </a>
                        </td>
                        <td>{request.quantity}</td>
                        <td>{formatNumber(request.price)}</td>
                        <td>{request.vendor}</td>
                        <td
                          className={
                            request.status === "Approved"
                              ? "table-success"
                              : request.status === "Denied"
                              ? "table-danger"
                              : "table-secondary"
                          }
                        >
                          {request.status}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </Tab.Pane>

              <Tab.Pane eventKey="third">
                <h3>Expense Requests</h3>
                <Table striped bordered hover responsive>
                  <thead>
                    <tr>
                      <th>Details</th>
                      <th>Amount</th>
                      <th>Type</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {grant.expenseRequests.map((request) => (
                      <tr>
                        <td>
                          <a href={`/requests/expense/${request._id}`}>
                            {request.details}
                          </a>
                        </td>
                        <td>{formatNumber(request.amount)}</td>
                        <td>{request.expenseRequestType}</td>
                        <td
                          className={
                            request.status === "Approved"
                              ? "table-success"
                              : request.status === "Denied"
                              ? "table-danger"
                              : "table-secondary"
                          }
                        >
                          {request.status}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </Tab.Pane>
              <Tab.Pane eventKey="fourth">
                <h3>Reallocation Requests</h3>
                <Table striped bordered hover responsive>
                  <thead>
                    <tr>
                      <th>Justification</th>
                      <th>From</th>
                      <th>To</th>
                      <th>Amount</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {grant.reallocationRequests.map((request) => (
                      <tr>
                        <td>
                          <a href={`/requests/reallocation/${request._id}`}>
                            {request.justification}
                          </a>
                        </td>
                        <td>{request.from}</td>
                        <td>{request.to}</td>
                        <td>{formatNumber(request.amount)}</td>
                        <td
                          className={
                            request.status === "Approved"
                              ? "table-success"
                              : request.status === "Denied"
                              ? "table-danger"
                              : "table-secondary"
                          }
                        >
                          {request.status}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </Tab.Pane>
            </Tab.Content>
          </Col>
        </Row>
      </Tab.Container>
    </div>
  );
};

export default GrantRequests;
