import React from "react";
import Comment from "./Comment";
import CommentForm from "./CommentForm";
import commentService from "../../services/commentService";

const CommentsList = ({ comments, postId, postType, setComments }) => {
  const createComment = (text) => {
    commentService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    commentService
      .createComment({
        text,
        postType,
        postId,
      })
      .then((createdComment) => {
        createdComment.isNew = true;
        setComments([...comments, createdComment]);
        window.scrollTo(0, document.body.scrollHeight);
      })
      .catch((err) => console.log(err));
  };

  const updateComment = (text, commentId) => {
    commentService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    commentService
      .updateComment(text, commentId)
      .then((updatedComment) => {
        setComments(
          comments.map((comment) =>
            comment._id !== commentId ? comment : updatedComment
          )
        );
      })
      .catch((err) => console.log(err));
  };

  const deleteComment = (id) => {
    commentService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    commentService
      .deleteComment(id)
      .then(() => setComments(comments.filter((comment) => comment._id !== id)))
      .catch((err) => console.log(err));
  };

  return (
    <div>
      <CommentForm createComment={createComment} />
      {comments.map((comment) => (
        <Comment
          key={comment._id}
          comment={comment}
          updateComment={updateComment}
          deleteComment={deleteComment}
        />
      ))}
    </div>
  );
};

export default CommentsList;
