import React, { useState, useEffect } from "react";

import requestService from "../../services/requestService";
import { useHistory } from "react-router-dom";
import PurchaseReqForm from "./PurchaseReqForm";
import ResearchReqForm from "./ResearchReqForm";
import ExpenseReqForm from "./ExpenseReqForm";
import ReallocationRequestForm from "./ReallocationRequestForm";
import Form from "react-bootstrap/Form";
import { store } from "react-notifications-component";
import Alert from "react-bootstrap/Alert";
import grantService from "../../services/grantService";

const RequestForm = ({ setModalState2 }) => {
  const [researchReq, setResearchReq] = useState(false);
  const [purchaseReq, setPurchaseReq] = useState(false);
  const [expenseReq, setExpenseReq] = useState(false);
  const [reallocationReq, setReallocationReq] = useState(false);

  const [grants, setGrants] = useState([]);
  const [filteredGrants, setFilteredGrants] = useState([]);
  const [selectedGrant, setSelectedGrant] = useState("");

  const [pis, setPis] = useState([]);
  const [selectedPI, setSelectedPI] = useState(false);

  const [notification, setNotification] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const history = useHistory();
  useEffect(() => {
    grantService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    grantService.getAll().then((foundGrants) => {
      setGrants(foundGrants);

      setPis([...new Set(foundGrants.map((grant) => grant.pi.name))]);
    });
  }, []);
  const selectForm = (event) => {
    event.preventDefault();
    if (event.target.value === "Purchase Request") {
      setPurchaseReq(true);
      setResearchReq(false);
      setExpenseReq(false);
      setReallocationReq(false);
    } else if (event.target.value === "Research Assistant Request") {
      setResearchReq(true);
      setPurchaseReq(false);
      setExpenseReq(false);
      setReallocationReq(false);
    } else if (event.target.value === "Expense Request") {
      setPurchaseReq(false);
      setResearchReq(false);
      setExpenseReq(true);
      setReallocationReq(false);
    } else if (event.target.value === "Reallocation Request") {
      setPurchaseReq(false);
      setResearchReq(false);
      setExpenseReq(false);
      setReallocationReq(true);
    } else {
      setPurchaseReq(false);
      setResearchReq(false);
      setExpenseReq(false);
    }
  };

  const submitRequest = (newReq, type) => {
    requestService
      .createRequest(newReq)
      .then((createdRequest) => {
        console.log(createdRequest);
        setModalState2(false, false, false);
        setTimeout(
          () => history.push(`/requests/${type}/${createdRequest._id}`),
          1000
        );
        store.addNotification({
          title: "Created New Request!",
          message: "Success!",
          type: "success",
          insert: "top",
          container: "bottom-left",
          animationIn: ["animated", "fadeIn"],
          animationOut: ["animated", "fadeOut"],
          dismiss: {
            duration: 5000,
            onScreen: true,
          },
        });
      })
      .catch((err) => displayError(err.response.data.error));
  };

  const submitPurchaseRequest = (newReq) => {
    requestService.setEndpoint("purchase");
    console.log(newReq);
    submitRequest({ ...newReq, grantId: selectedGrant }, "purchase");
  };

  const submitRARequest = (newReq) => {
    requestService.setEndpoint("research");
    console.log(newReq);
    submitRequest({ ...newReq, grantId: selectedGrant }, "research");
  };

  const submitExpenseRequest = (newReq) => {
    requestService.setEndpoint("expense");
    console.log(newReq);
    submitRequest({ ...newReq, grantId: selectedGrant }, "expense");
  };

  const submitReallocationRequest = (newReq) => {
    requestService.setEndpoint("reallocation");
    console.log(newReq);
    submitRequest({ ...newReq, grantId: selectedGrant }, "reallocation");
  };

  const displayError = (err) => {
    console.log(err);
    setErrorMessage(err);
    setNotification(true);
    setTimeout(() => {
      setNotification(false);
    }, 5000);
  };

  const formSelector = () => {
    if (purchaseReq) {
      return <PurchaseReqForm submitPurchaseRequest={submitPurchaseRequest} />;
    }
    if (researchReq) {
      return <ResearchReqForm submitRARequest={submitRARequest} />;
    }
    if (expenseReq) {
      return <ExpenseReqForm submitExpenseRequest={submitExpenseRequest} />;
    }
    if (reallocationReq) {
      return (
        <ReallocationRequestForm
          submitReallocationRequest={submitReallocationRequest}
        />
      );
    }
    return null;
  };

  const selectGrant = (event) => {
    //console.log(event.target.value.split("-")[1])
    setSelectedGrant(
      filteredGrants.find((grant) => grant.title === event.target.value)
    );
  };

  const selectPI = (event) => {
    setSelectedPI(event.target.value);
    setFilteredGrants(
      grants.filter((grant) => grant.pi.name === event.target.value)
    );
  };

  return (
    <div>
      {notification ? <Alert variant={"danger"}>{errorMessage}</Alert> : null}
      <Form>
        {grants ? (
          <Form.Group controlId="exampleForm.SelectPI">
            <Form.Label>Select PI</Form.Label>
            <Form.Control as="select" onChange={selectPI}>
              <option defaultValue>Select</option>
              {pis.map((pi) => (
                <option>{pi}</option>
              ))}
            </Form.Control>
          </Form.Group>
        ) : null}
        {selectedPI ? (
          <Form.Group controlId="exampleForm.SelectGrant">
            <Form.Label>Select Grant</Form.Label>
            <Form.Control as="select" onChange={selectGrant}>
              <option value="">Select</option>
              {filteredGrants.map((grant) => (
                <option key={grant._id} value={grant.title}>
                  {grant.costCenter}-{grant.title}
                </option>
              ))}
            </Form.Control>
          </Form.Group>
        ) : null}

        <Form.Group controlId="exampleForm.SelectCustom">
          <Form.Label>Select Request Type</Form.Label>
          <Form.Control as="select" onChange={selectForm}>
            <option defaultValue>Select</option>
            <option>Research Assistant Request</option>
            <option>Purchase Request</option>
            <option>Expense Request</option>
            <option>Reallocation Request</option>
          </Form.Control>
        </Form.Group>
      </Form>
      {formSelector()}
    </div>
  );
};

export default RequestForm;
