import React, { useState } from "react";
import ListGroup from "react-bootstrap/ListGroup";

const ListItem = ({
  value,
  variantProp,
  fieldId,
  setField,
  setFieldSet,
  setFieldId,
}) => {
  const [variant, setVariant] = useState(variantProp);

  return (
    <ListGroup.Item
      variant={variant}
      onMouseEnter={() => setVariant("primary")}
      onMouseLeave={() => setVariant(variantProp)}
      onClick={() => {
        setField(value);
        setFieldId(fieldId);
        setFieldSet(true);
      }}
    >
      {value}
    </ListGroup.Item>
  );
};

export default ListItem;
