import axios from "axios";

const baseUrl = "/api/grants";
//http://localhost:3001
let token = null;

const setToken = (newToken) => {
  token = `bearer ${newToken}`;
  config.headers.Authorization = token;
};

const config = {
  headers: { Authorization: token },
};

const getAll = async () => {
  const response = await axios.get(baseUrl, config);
  return response.data.grants;
};

const getOne = async (id) => {
  const response = await axios.get(`${baseUrl}/${id}`, config);
  return response.data;
};

const createGrant = async (newGrant) => {
  const response = await axios.post(baseUrl, newGrant, config);
  return response.data;
};

const updateGrant = async (newGrant) => {
  const response = await axios.put(
    `${baseUrl}/${newGrant._id}`,
    newGrant,
    config
  );
  return response.data;
};

const deleteGrant = async (id) => {
  const config = {
    headers: { Authorization: token },
  };
  const response = await axios.delete(`${baseUrl}/${id}`, config);
  return response.status;
};

const deleteGrants = async (deleteObject) => {
  const config = {
    headers: { Authorization: token },
  };
  console.log(config);
  const response = await axios.delete(`${baseUrl}/deletemany`, {
    data: deleteObject,
    headers: { Authorization: token },
  });
  return response.status;
};
/*
const getReport = async (reportType) => {
  const response = await axios.get(`${baseUrl}/counter/${reportType}`, config);
  return response.data;
};*/

const getReport = async (reporturl) => {
  const response = await axios.get(`${baseUrl}/${reporturl}`, config);
  return response.data;
};

export default {
  getAll,
  updateGrant,
  deleteGrant,
  deleteGrants,
  createGrant,
  getOne,
  setToken,
  getReport,
};
