import React, { useState } from "react";
import useField from "../../hooks/useField";

import axios from "axios";
import Form from "react-bootstrap/Form";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";

const ExpenseReqForm = ({ submitExpenseRequest }) => {
  const details = useField("text");
  const amount = useField("number");
  const currency = useField("text");
  const dateCreated = useField("date");
  const [file, setFile] = useState(null);
  const expenseRequestType = useField();

  const [errMessage, setErrMessage] = useState("");

  const submitRequest = (event) => {
    event.preventDefault();
    const newReq = {
      details: details.value,
      amount: amount.value.toString(),
      currency: currency.value,
      dateCreated: dateCreated.value,
      expenseRequestType: expenseRequestType.value,
    };

    const { validated, errorStr } = validator();
    if (!validated) {
      setErrMessage(errorStr);
      setTimeout(() => setErrMessage(""), 4000);
    } else {
      if (file) {
        if (file.size > 10000000) {
          setErrMessage("File Must be under 10 MB");
          setTimeout(() => setErrMessage(""), 4000);
          return;
        } else if (
          file.type !==
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" &&
          file.type !==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        ) {
          setErrMessage("File Must A Word or Excel File");
          setTimeout(() => setErrMessage(""), 4000);
          return;
        }
        onClickHandler();
        setFile(null);
      }
      submitExpenseRequest(newReq);
    }
  };

  const onChangeHandler = (event) => {
    console.log(event.target.files[0]);
    setFile(event.target.files[0]);
  };
  const onClickHandler = () => {
    const data = new FormData();
    data.append("file", file);
    axios
      .post("/upload", data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res) => {
        // then print response status
        console.log(res.statusText);
      })
      .catch((err) => console.log(err));
  };

  const validator = () => {
    let errorStr = "";
    if (
      !details.value ||
      !amount.value ||
      !currency.value ||
      !dateCreated.value ||
      !expenseRequestType.value
    ) {
      errorStr += "Field is Missing";
      return { validated: false, errorStr };
    }

    if (details.value.length < 2) {
      errorStr += " Details must be at least 2 characters long";
      return { validated: false, errorStr };
    }

    return { validated: true, errorStr };
  };

  return (
    <Form>
      <h3>New Expense Request</h3>
      {errMessage ? <Alert variant={"danger"}>{errMessage}</Alert> : null}
      <div>
        {" "}
        <Button
          variant="success"
          onClick={() => {
            window.open(
              "http://localhost:3001/files/download/Expense_Reimbursement_Form.xlsx  "
            );
          }}
        >
          Download Expense Reimbursement Form
        </Button>
        <br />
      </div>
      <Form.Group controlId="exampleForm.typeInput">
        <Form.Label>Expense Request Type</Form.Label>
        <Form.Control
          as="select"
          onChange={expenseRequestType.onChange}
          value={expenseRequestType.value}
        >
          {!expenseRequestType.value ? (
            <option value="" disabled defaultValue>
              Enter Expense Request Type
            </option>
          ) : null}
          <option>Conference Travel</option>
          <option>Publication Fees</option>
          <option>Items Purchase</option>
          <option>Software/Hardware Purchase</option>
          <option>Others</option>
        </Form.Control>
      </Form.Group>
      <Form.Row>
        <Form.Group as={Col} controlId="formGridDetails">
          <Form.Label>Details</Form.Label>
          <Form.Control {...details} placeholder="Enter Item Details" />
        </Form.Group>

        <Form.Group as={Col} controlId="formGridQuantity">
          <Form.Label>Currency</Form.Label>
          <Form.Control {...currency} placeholder="Enter Currency" />
        </Form.Group>
      </Form.Row>

      <Form.Row>
        <Form.Group as={Col} controlId="formGridPrice">
          <Form.Label>Amount</Form.Label>
          <Form.Control min="0" {...amount} placeholder="Enter Amount" />
        </Form.Group>

        <Form.Group as={Col} controlId="formGridVendor">
          <Form.Label>Date </Form.Label>
          <Form.Control {...dateCreated} />
        </Form.Group>
      </Form.Row>
      <label>Request File (optional)</label>
      <div className="input-group">
        <div className="custom-file">
          <input
            type="file"
            name="file"
            className="custom-file-input"
            id="inputGroupFile01"
            aria-describedby="inputGroupFileAddon01"
            onChange={onChangeHandler}
          />
          <label className="custom-file-label" htmlFor="inputGroupFile01">
            {file ? file.name : "Choose file"}
          </label>
        </div>
      </div>
      <Button variant="primary" type="submit" onClick={submitRequest}>
        Submit
      </Button>
    </Form>
  );
};

export default ExpenseReqForm;
