import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";

import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import requestService from "../../services/requestService";
import CommentsList from "../Comments/CommentsList";

const Request = ({ addNotification }) => {
  let { id, type } = useParams();
  const [request, setRequest] = useState(null);
  const [comments, setComments] = useState(null);
  useEffect(() => {
    requestService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    requestService.setEndpoint(type);
    requestService
      .getOne(id)
      .then((foundRequest) => {
        console.log(foundRequest);
        setRequest(foundRequest);
        setComments(foundRequest.comments);
      })
      .catch((err) => console.log(err));
  }, []);

  const updateRequestStatus = (status) => {
    requestService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    requestService.setEndpoint(type);
    requestService
      .updateStatus(status, request._id)
      .then((updatedRequest) => {
        console.log("UPD", updatedRequest);
        //setRequest(updatedRequest); //FIX

        window.location.href = `/requests/${type}/${id}`;
      })
      .catch((err) => console.log(err));
  };

  const deleteRequest = () => {
    requestService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    requestService.setEndpoint(type);
    requestService
      .deleteRequest(request._id)
      .then((deletedRequest) => {
        console.log("DEL", deletedRequest);
        window.location.href = `/grants/${request.grantId._id}`;
      })
      .catch((err) => console.log(err));
  };

  const setPostType = () => {
    switch (type) {
      case "purchase":
        return "PurchaseRequest";
      case "research":
        return "RARequest";
      case "expense":
        return "ExpenseRequest";
      case "reallocation":
        return "ReallocationRequest";
      default:
        return "";
    }
  };
  const renderByType = () => {
    return (
      <Table striped bordered hover responsive>
        <tbody>
          {type === "purchase" ? (
            <>
              <tr>
                <th>Name</th>
                <td>{request.name}</td>
              </tr>
              <tr>
                <th>Requester</th>
                <td>{request.requester.name}</td>
              </tr>
              <tr>
                <th>Quantity</th>
                <td>{request.quantity}</td>
              </tr>
              <tr>
                <th>Price</th>
                <td>{request.price}</td>
              </tr>
              <tr>
                <th>Vendor</th>
                <td>{request.vendor}</td>
              </tr>
              <tr>
                <th>Link</th>
                <td>{request.link}</td>
              </tr>
              <tr>
                <th>Status</th>
                <td>{request.status}</td>
              </tr>
              <tr>
                <th>Request</th>
                <td>
                  <a href={`/grants/${request.grantId._id}`}>
                    {request.grantId.title}
                  </a>
                </td>
              </tr>
            </>
          ) : null}

          {type === "research" ? (
            <>
              <tr>
                <th>Name</th>
                <td>{request.name}</td>
              </tr>
              <tr>
                <th>Requester</th>
                <td>{request.requester.name}</td>
              </tr>
              <tr>
                <th>Salary</th>
                <td>{request.salary}</td>
              </tr>
              <tr>
                <th>Start Date</th>
                <td>{new Date(request.startDate).toDateString()}</td>
              </tr>
              <tr>
                <th>End Date</th>
                <td>{new Date(request.endDate).toDateString()}</td>
              </tr>
              <tr>
                <th>Status</th>
                <td>{request.status}</td>
              </tr>
              <tr>
                <th>Request</th>
                <td>
                  <a href={`/grants/${request.grantId._id}`}>
                    {request.grantId.title}
                  </a>
                </td>
              </tr>
            </>
          ) : null}
          {type === "expense" ? (
            <>
              <tr>
                <th>Details</th>
                <td>{request.details}</td>
              </tr>
              <tr>
                <th>Requester</th>
                <td>{request.requester.name}</td>
              </tr>
              <tr>
                <th>Currency</th>
                <td>{request.currency}</td>
              </tr>
              <tr>
                <th>Amount</th>
                <td>{request.amount}</td>
              </tr>
              <tr>
                <th>Date Created</th>
                <td>{new Date(request.dateCreated).toDateString()}</td>
              </tr>
              <tr>
                <th>Type</th>
                <td>{request.expenseRequestType}</td>
              </tr>
              <tr>
                <th>Status</th>
                <td>{request.status}</td>
              </tr>
              <tr>
                <th>Request</th>
                <td>
                  <a href={`/grants/${request.grantId._id}`}>
                    {request.grantId.title}
                  </a>
                </td>
              </tr>
            </>
          ) : null}
          {type === "reallocation" ? (
            <>
              <tr>
                <th>From</th>
                <td>{request.from}</td>
              </tr>
              <tr>
                <th>To</th>
                <td>{request.to}</td>
              </tr>
              <tr>
                <th>Amount</th>
                <td>{request.amount}</td>
              </tr>
              <tr>
                <th>Justification</th>
                <td>{request.justification}</td>
              </tr>
              <tr>
                <th>Status</th>
                <td>{request.status}</td>
              </tr>
              <tr>
                <th>Requester</th>
                <td>{request.requester.name}</td>
              </tr>
              <tr>
                <th>Request</th>
                <td>
                  <a href={`/grants/${request.grantId._id}`}>
                    {request.grantId.title}
                  </a>
                </td>
              </tr>
            </>
          ) : null}
        </tbody>
      </Table>
    );
  };
  return (
    <div>
      <h3>{type.toUpperCase()} Request</h3>

      {JSON.parse(window.localStorage.getItem("loggedInUser")).userType ===
      "admin" ? (
        request && request.status === "Pending" ? (
          <>
            <Button
              variant="success"
              onClick={() => updateRequestStatus("Approved")}
            >
              Approve
            </Button>
            <Button
              variant="danger"
              onClick={() => updateRequestStatus("Denied")}
            >
              Deny
            </Button>
          </>
        ) : null
      ) : null}
      {JSON.parse(window.localStorage.getItem("loggedInUser")).userType ===
      "admin" ? (
        <Button variant="danger" onClick={deleteRequest}>
          Delete
        </Button>
      ) : null}

      {request ? renderByType() : null}
      {request ? (
        <CommentsList
          comments={comments ? comments : []}
          setComments={setComments}
          postId={request._id}
          postType={setPostType()}
        />
      ) : null}
    </div>
  );
};

export default Request;
