import React from "react";
import Table from "react-bootstrap/Table";

import { Tab, Tabs } from "react-bootstrap";




const RequestsTable = ({
  researchData,
  expenseData,
  purchaseData,
  reallocationData,
}) => {
  return (
    <div>
      <Tabs defaultActiveKey="ra" id="uncontrolled-tab-example">
        <Tab eventKey="ra" title="RA Requests">
          <h3>RA REQUESTS</h3>
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th># </th>
                <th>Requester</th>
                <th>Name</th>
                <th>Salary</th>
                <th>Start Date</th>
                <th>End Date</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {researchData.map((req) =>
                researchData.indexOf(req) !== -1 ? (
                  <tr key={req._id}>
                    <td className="table-info">
                      {researchData.indexOf(req) + 1}
                    </td>

                    <td className="table-success">
                      <a href={`/requests/research/${req._id}`}>{req.name}</a>
                    </td>

                    <td className="table-primary">{req.grantId.pi}</td>

                    <td className="table-warning">{req.salary}</td>
                    <td className="table-secondary">
                      {new Date(req.startDate).toDateString()}
                    </td>
                    <td className="table-primary">
                      {new Date(req.endDate).toDateString()}
                    </td>
                    <td className="table-danger">{req.status}</td>
                  </tr>
                ) : null
              )}
            </tbody>
          </Table>
        </Tab>
        <Tab eventKey="purchase" title="Purchase Requests">
          <h3>PURCHASE REQUESTS</h3>
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th># </th>
                <th>Name</th>
                <th>Requester</th>
                <th>Quantity</th>
                <th>Price</th>
                <th>Vendor</th>
                <th>Link</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {purchaseData.map((req) =>
                purchaseData.indexOf(req) !== -1 ? (
                  <tr key={req._id}>
                    <td className="table-info">
                      {purchaseData.indexOf(req) + 1}
                    </td>
                    <td className="table-success">
                      <a href={`/requests/purchase/${req._id}`}>{req.name}</a>
                    </td>
                    <td className="table-primary">{req.grantId.pi}</td>
                    <td className="table-warning">{req.quantity}</td>
                    <td className="table-secondary">{req.price}</td>
                    <td className="table-primary">{req.vendor}</td>
                    <td className="table-info">{req.link}</td>
                    <td className="table-danger">{req.status}</td>
                  </tr>
                ) : null
              )}
            </tbody>
          </Table>
        </Tab>
        <Tab eventKey="expense" title="Expense Requests">
          <h3>EXPENSE REQUESTS</h3>
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th># </th>
                <th>Details</th>
                <th>Requester</th>
                <th>Date Created</th>
                <th>Currency</th>
                <th>Amount</th>
                <th>Status</th>
                <th>Type</th>
              </tr>
            </thead>
            <tbody>
              {expenseData.map((req) =>
                expenseData.indexOf(req) !== -1 ? (
                  <tr key={req._id}>
                    <td className="table-info">
                      {expenseData.indexOf(req) + 1}
                    </td>
                    <td className="table-success">
                      <a href={`/requests/expense/${req._id}`}>{req.details}</a>
                    </td>
                    <td className="table-primary">{req.grantId.pi}</td>
                    <td className="table-warning">
                      {new Date(req.dateCreated).toDateString()}
                    </td>
                    <td className="table-secondary">{req.currency}</td>
                    <td className="table-primary">{req.amount}</td>
                    <td className="table-danger">{req.status}</td>
                    <td className="table-danger">{req.expenseRequestType}</td>
                  </tr>
                ) : null
              )}
            </tbody>
          </Table>
        </Tab>
        <Tab eventKey="reallocation" title="Reallocation Requests">
          <h3>REALLOCATION REQUESTS</h3>
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th># </th>
                <th>Justification</th>
                <th>Requester</th>
                <th>From</th>
                <th>To</th>
                <th>Amount</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {reallocationData.map((req) =>
                reallocationData.indexOf(req) !== -1 ? (
                  <tr key={req._id}>
                    <td className="table-info">
                      {reallocationData.indexOf(req) + 1}
                    </td>
                    <td className="table-success">
                      <a href={`/requests/reallocation/${req._id}`}>
                        {req.justification}
                      </a>
                    </td>
                    <td className="table-primary">{req.grantId.pi}</td>
                    <td className="table-warning">{req.from}</td>
                    <td className="table-secondary">{req.to}</td>
                    <td className="table-primary">{req.amount}</td>
                    <td className="table-danger">{req.status}</td>
                  </tr>
                ) : null
              )}
            </tbody>
          </Table>
        </Tab>
      </Tabs>
    </div>
  );
};

export default RequestsTable;
