import React, { useState, useEffect } from "react";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Accordion from "react-bootstrap/Accordion";
import Card from "react-bootstrap/Card";
import Alert from "react-bootstrap/Alert";
import userService from "../../services/userService";
import useField from "../../hooks/useField";

const ProfilePage = ({ addNotification }) => {
  const [user, setUser] = useState(null);
  const [updating, setUpdating] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  const name = useField();
  const email = useField();
  const location = useField();
  const phoneNumber = useField();
  const role = useField();
  const gender = useField();

  const oldPassword = useField("password");
  const newPassword = useField("password");
  const confirmNewPassword = useField("password");

  useEffect(() => {
    const loggedInUser = JSON.parse(
      window.localStorage.getItem("loggedInUser")
    );

    userService.setToken(loggedInUser.token);

    userService
      .getUser(loggedInUser.userType, loggedInUser.id)
      .then((foundUser) => {
        console.log(foundUser);
        setUser(foundUser);
      })
      .catch((err) => {
        console.log(err.message.response.data.error);
        setErrorMessage(err.response.data.error);
        setTimeout(() => setErrorMessage(""), 5000);
      });
  }, []);

  const initializeForm = () => {
    name.setValue(user.name);
    email.setValue(user.email);
    location.setValue(user.location);
    phoneNumber.setValue(user.phoneNumber);
    role.setValue(user.role);
    gender.setValue(user.gender);
    oldPassword.setValue("");
    newPassword.setValue("");
    confirmNewPassword.setValue("");
  };

  const updateUser = () => {
    if (oldPassword.value || newPassword.value || confirmNewPassword.value) {
      if (newPassword.value !== confirmNewPassword.value) {
        setErrorMessage("New password does not match with confirm password");
        setTimeout(() => setErrorMessage(""), 5000);
        return;
      }
    }

    const loggedInUser = JSON.parse(
      window.localStorage.getItem("loggedInUser")
    );

    userService.setToken(loggedInUser.token);

    const updatedUser = {
      name: name.value,
      gender: gender.value,
      phoneNumber: phoneNumber.value,
      role: role.value,
      location: location.value,
      email: email.value,
      oldPassword: oldPassword.value,
      newPassword: newPassword.value,
      confirmNewPassword: confirmNewPassword.value,
    };
    userService
      .updateUser(loggedInUser.userType, loggedInUser.id, updatedUser)
      .then((newUser) => {
        console.log(newUser);
        setUser(newUser);
        addNotification("Updated User Info");
      })
      .catch((err) => {
        console.log(err.response.data.error);
        setErrorMessage(err.response.data.error);
        setTimeout(() => setErrorMessage(""), 5000);
      });
  };
  return (
    <div>
      {user ? (
        <>
          <h3>User Settings</h3>
          {errorMessage ? (
            <Alert variant={"danger"}>{errorMessage}</Alert>
          ) : null}
          <Button
            onClick={() => {
              if (!updating) {
                setUpdating(true);
                initializeForm();
              } else {
                console.log({
                  name: name.value,
                  gender: gender.value,
                  phoneNumber: phoneNumber.value,
                  role: role.value,
                  location: location.value,
                  email: email.value,
                });
                updateUser();
                setUpdating(false);
              }
            }}
          >
            {updating ? "Update" : "Edit"}
          </Button>
          {updating ? (
            <Button
              variant="success"
              onClick={() => {
                setUpdating(false);
                initializeForm();
              }}
            >
              Cancel
            </Button>
          ) : null}
          <Table striped bordered hover responsive>
            <tbody>
              <tr>
                <th className="table-success">Name</th>
                {!updating ? (
                  <td className="settingsRow">{user.name}</td>
                ) : (
                  <Form.Control
                    type="text"
                    {...name}
                    placeholder="Enter Name"
                  />
                )}
              </tr>
              <tr>
                <th className="table-success">Email</th>
                {!updating ? (
                  <td className="settingsRow">{user.email}</td>
                ) : (
                  <Form.Control
                    type="email"
                    {...email}
                    placeholder="Enter Email"
                  />
                )}
              </tr>

              <tr>
                <th className="table-success">Gender</th>
                {!updating ? (
                  <td className="settingsRow">{user.gender}</td>
                ) : (
                  <Form.Control {...gender} as="select" custom>
                    <option></option>
                    <option>Male</option>
                    <option>Female</option>
                  </Form.Control>
                )}
              </tr>
              <tr>
                <th className="table-success">Phone Number</th>
                {!updating ? (
                  <td className="settingsRow">{user.phoneNumber}</td>
                ) : (
                  <Form.Control
                    type="number"
                    {...phoneNumber}
                    placeholder="Enter Phone Number"
                  />
                )}
              </tr>
              <tr>
                <th className="table-secondary">Location</th>
                {!updating ? (
                  <td className="settingsRow">{user.location}</td>
                ) : (
                  <Form.Control
                    type="text"
                    {...location}
                    placeholder="Enter Location"
                  />
                )}
              </tr>
              <tr>
                <th className="table-warning">Role</th>
                {!updating ? (
                  <td className="settingsRow">{user.role}</td>
                ) : (
                  <Form.Control {...role} as="select" custom>
                    <option></option>
                    <option>Admin</option>
                    <option>Faculty</option>
                    <option>Staff</option>
                    <option>Student</option>
                  </Form.Control>
                )}
              </tr>
            </tbody>
          </Table>
          {updating ? (
            <Accordion>
              <Card>
                <Card.Header>
                  <Accordion.Toggle as={Button} variant="link" eventKey="0">
                    Update Password
                  </Accordion.Toggle>
                </Card.Header>
                <Accordion.Collapse eventKey="0">
                  <Card.Body>
                    <Table striped bordered hover responsive>
                      <tbody>
                        <tr>
                          <th className="table-success">Old Password</th>

                          <td>
                            <Form.Control {...oldPassword}></Form.Control>
                          </td>
                        </tr>
                        <tr>
                          <th className="table-primary">New Password</th>
                          <td>
                            {" "}
                            <Form.Control {...newPassword}></Form.Control>
                          </td>
                        </tr>

                        <tr>
                          <th className="table-primary">
                            Confirm New Password
                          </th>
                          <td>
                            {" "}
                            <Form.Control
                              {...confirmNewPassword}
                            ></Form.Control>
                          </td>
                        </tr>
                      </tbody>
                    </Table>
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            </Accordion>
          ) : null}
        </>
      ) : null}
    </div>
  );
};

export default ProfilePage;
