import React, { useState } from "react";
import useField from "../../hooks/useField";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import { Form } from "react-bootstrap";

const GrantBudget = ({ grant, updateGrant }) => {
  const [editing, setEditing] = useState(false);

  const approvedBudget = useField("number");
  const availableBudget = useField("number");
  const totalExpenses = useField("number");
  const reservedBudget = useField("number");
  const rABudget = useField("number");
  const conferenceBudget = useField("number");
  const softwareAndHardwareBudget = useField("number");
  const otherItemBudget = useField("number");

  const isAdmin =
    JSON.parse(window.localStorage.getItem("loggedInUser")).userType ===
    "admin";
  const formatNumber = (num) =>
    num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");

  const renderTableData = (budget, fieldHook) => {
    if (!editing) {
      return <td>{formatNumber(budget)}</td>;
    } else {
      return (
        <Form.Group>
          <Form.Control {...fieldHook} />
        </Form.Group>
      );
    }
  };

  const resetFields = () => {
    approvedBudget.setValue(grant.approvedBudget);
    availableBudget.setValue(grant.availableBudget);
    reservedBudget.setValue(grant.reservedBudget);
    totalExpenses.setValue(grant.totalExpenses);
    rABudget.setValue(grant.rABudget);
    conferenceBudget.setValue(grant.conferenceBudget);
    softwareAndHardwareBudget.setValue(grant.softwareAndHardwareBudget);
    otherItemBudget.setValue(grant.otherItemBudget);
  };

  const update = () => {
    const budgetObject = {
      approvedBudget: approvedBudget.value
        ? Number(approvedBudget.value)
        : grant.approvedBudget,
      availableBudget: availableBudget.value
        ? Number(availableBudget.value)
        : grant.availableBudget,
      reservedBudget: reservedBudget.value
        ? Number(reservedBudget.value)
        : grant.reservedBudget,
      totalExpenses: totalExpenses.value
        ? Number(totalExpenses.value)
        : grant.totalExpenses,
      rABudget: rABudget.value ? Number(rABudget.value) : grant.rABudget,
      conferenceBudget: conferenceBudget.value
        ? Number(conferenceBudget.value)
        : grant.conferenceBudget,
      softwareAndHardwareBudget: softwareAndHardwareBudget.value
        ? Number(softwareAndHardwareBudget.value)
        : grant.softwareAndHardwareBudget,
      otherItemBudget: otherItemBudget.value
        ? Number(otherItemBudget.value)
        : grant.otherItemBudget,
    };

    console.log(budgetObject);
    updateGrant(budgetObject);
    resetFields();
    setEditing(false);
  };

  const cancel = () => {
    setEditing(false);
    resetFields();
  };

  return (
    <>
      {isAdmin ? (
        <Button
          variant={editing ? "danger" : "primary"}
          onClick={() => {
            if (editing) {
              update();
            } else {
              resetFields();
              setEditing(!editing);
            }
          }}
        >
          {editing ? "Update" : "Edit Budgets"}
        </Button>
      ) : null}
      {isAdmin && editing ? (
        <Button variant="primary" onClick={cancel}>
          Cancel
        </Button>
      ) : null}
      <Table striped bordered hover responsive>
        <tbody>
          <tr>
            <th className="table-success">Approved Budget</th>

            {renderTableData(grant.approvedBudget, approvedBudget)}
          </tr>
          <tr>
            <th className="table-success">Available Budget</th>

            {renderTableData(grant.availableBudget, availableBudget)}
          </tr>

          <tr>
            <th className="table-success">Total Expenses</th>

            {renderTableData(grant.totalExpenses, totalExpenses)}
          </tr>
          <tr>
            <th className="table-secondary">Reserved Budget</th>

            {renderTableData(grant.reservedBudget, reservedBudget)}
          </tr>
          <tr>
            <th className="table-warning">RA Budget</th>

            {renderTableData(grant.rABudget, rABudget)}
          </tr>

          <tr>
            <th className="table-warning">Conference Budget</th>

            {renderTableData(grant.conferenceBudget, conferenceBudget)}
          </tr>

          <tr>
            <th className="table-warning">Software/Hardware Budget</th>

            {renderTableData(
              grant.softwareAndHardwareBudget,
              softwareAndHardwareBudget
            )}
          </tr>
          <tr>
            <th className="table-warning">Other Item Budget</th>

            {renderTableData(grant.otherItemBudget, otherItemBudget)}
          </tr>
        </tbody>
      </Table>
    </>
  );
};

export default GrantBudget;
