import React, { useState } from "react";
import useField from "../../hooks/useField";

import axios from "axios";
import Form from "react-bootstrap/Form";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";

const PurchaseReqForm = ({ submitPurchaseRequest }) => {
  const name = useField("text");
  const quantity = useField("number");
  const price = useField("number");
  const vendor = useField("text");
  const url = useField("text");
  const [file, setFile] = useState(null);
  const [errMessage, setErrMessage] = useState("");

  const submitRequest = (event) => {
    event.preventDefault();
    const newPurchaseRequest = {
      name: name.value,
      quantity: quantity.value < 1 ? 1 : Math.abs(quantity.value),
      price: Math.abs(price.value),
      vendor: vendor.value,
      link: url.value,
    };
    const { validated, errorStr } = validator();
    if (!validated) {
      setErrMessage(errorStr);
      setTimeout(() => setErrMessage(""), 4000);
    } else {
      if (file) {
        if (file.size > 10000000) {
          setErrMessage("File Must be under 10 MB");
          setTimeout(() => setErrMessage(""), 4000);
          return;
        } else if (
          file.type !==
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" &&
          file.type !==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        ) {
          setErrMessage("File Must A Word or Excel File");
          setTimeout(() => setErrMessage(""), 4000);
          return;
        }
        onClickHandler();
        setFile(null);
      }
      submitPurchaseRequest(newPurchaseRequest);
    }
  };
  const onChangeHandler = (event) => {
    console.log(event.target.files[0]);
    setFile(event.target.files[0]);
  };
  const onClickHandler = () => {
    const data = new FormData();
    data.append("file", file);
    axios
      .post("/upload", data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res) => {
        // then print response status
        console.log(res.statusText);
      })
      .catch((err) => console.log(err));
  };
  const validator = () => {
    let errorStr = "";
    if (!name.value || !quantity.value || !price.value || !vendor.value) {
      errorStr += "Field is Missing";
      return { validated: false, errorStr };
    }

    if (name.value.length < 3) {
      errorStr += " Name must be at least 3 characters long";
      return { validated: false, errorStr };
    }

    return { validated: true, errorStr };
  };
  return (
    <Form>
      <h3>New Purchase Request</h3>
      {errMessage ? <Alert variant={"danger"}>{errMessage}</Alert> : null}

      <Form.Row>
        <Form.Group as={Col} controlId="formGridItemName">
          <Form.Label>Item Name</Form.Label>
          <Form.Control {...name} placeholder="Enter Item Name" />
        </Form.Group>
        <Form.Group as={Col} controlId="formGridVendor">
          <Form.Label>Vendor</Form.Label>
          <Form.Control {...vendor} placeholder="Enter Vendor" />
        </Form.Group>
      </Form.Row>

      <Form.Row>
        <Form.Group as={Col} controlId="formGridPrice">
          <Form.Label>Unit Price</Form.Label>
          <Form.Control min="0" {...price} placeholder="Enter Price" />
        </Form.Group>
        <Form.Group as={Col} controlId="formGridQuantity">
          <Form.Label>Quantity</Form.Label>
          <Form.Control min="1" {...quantity} placeholder="Enter Quantity" />
        </Form.Group>
      </Form.Row>
      <div>Total Price: {price.value * quantity.value}</div>
      <br />
      <Form.Group controlId="formGridUrl">
        <Form.Label>URL (optional)</Form.Label>
        <Form.Control {...url} placeholder="Enter URL to vendor" />
      </Form.Group>
      <label>Request File (optional)</label>
      <div className="input-group">
        <div className="custom-file">
          <input
            type="file"
            name="file"
            className="custom-file-input"
            id="inputGroupFile01"
            aria-describedby="inputGroupFileAddon01"
            onChange={onChangeHandler}
          />
          <label className="custom-file-label" htmlFor="inputGroupFile01">
            {file ? file.name : "Choose file"}
          </label>
        </div>
      </div>
      <Button variant="primary" type="submit" onClick={submitRequest}>
        Submit
      </Button>
    </Form>
  );
};

export default PurchaseReqForm;
