import React, { useState, useEffect } from "react";

import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Alert from "react-bootstrap/Alert";

import useField from "../../hooks/useField";
import userService from "../../services/userService";
import loginService from "../../services/loginService";
import { withRouter } from "react-router-dom";
import grantService from "../../services/grantService";
import requestService from "../../services/requestService";

const LoginForm = ({ useRegisterForm, setUser, setShowModal, history }) => {
  const password = useField("password");
  const email = useField("email");
  const name = useField("text");
  const [notification, setNotification] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  useEffect(
    () =>
      useRegisterForm
        ? window.history.pushState("", "", "/register")
        : window.history.pushState("", "", "/login"),
    []
  );
  const validateForm = () => {
    let errorStr = "";

    const validateEmail = () => {
      if (useRegisterForm) {
        const checkEmailFormat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(
          email.value
        );
        if (!email.value) {
          errorStr += " Email is required.";
        } else if (!checkEmailFormat) {
          errorStr += " Email not in valid format.";
        } else if (email.value.length < 5) {
          errorStr += " Email must be at least 5 characters.";
        }
      }
    };

    if (!name.value) {
      errorStr += " Email is required";
    } else if (name.value.length < 5) {
      errorStr += " Name must be at least 5 characters long.";
    }

    if (!password.value) {
      errorStr += " Password is required.";

      validateEmail();
      return { validated: false, errorStr };
    }
    if (password.value.length < 6) {
      errorStr += " Password should be at least 6 characters long.";

      validateEmail();
      return { validated: false, errorStr };
    }

    return { validated: true, errorStr };
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    const { validated, errorStr } = validateForm();
    if (!validated) {
      event.stopPropagation();
      displayError(errorStr);
      return;
    }

    userService
      .createUser(name.value, email.value, password.value, "user")
      .then((newUser) => {
        handleLogin();
      })
      .catch((err) => {
        displayError(err.response.data.error);
        console.log(err.response);
      });
  };

  const handleLogin = (event) => {
    if (event) {
      event.preventDefault();
      const { validated, errorStr } = validateForm();
      if (!validated) {
        event.stopPropagation();
        displayError(errorStr);
        return;
      }
    }

    loginService
      .login({
        email: email.value,
        password: password.value,
      })
      .then((loggedInUser) => {
        setUser(loggedInUser);
        password.value = "";
        email.value = "";
        window.localStorage.setItem(
          "loggedInUser",
          JSON.stringify(loggedInUser)
        );
        grantService.setToken(loggedInUser.token);
        requestService.setToken(loggedInUser.token);
        setShowModal(false);
        history.push("/grants");
      })
      .catch((err) => displayError(err.response.data.error));
  };

  const displayError = (err) => {
    console.log(err);
    setErrorMessage(err);
    setNotification(true);
    setTimeout(() => {
      setNotification(false);
    }, 5000);
  };
  return (
    <div>
      <Form>
        <Row>
          <Col>
            {notification ? (
              <Alert variant={"danger"}>{errorMessage}</Alert>
            ) : null}
          </Col>
        </Row>
        {useRegisterForm ? (
          <Row>
            <Col>
              <Form.Group controlId="formBasicEmail">
                <Form.Label>Name</Form.Label>
                <Form.Control {...name} placeholder="Enter name" />
              </Form.Group>
            </Col>
          </Row>
        ) : null}
        <Row>
          <Col>
            <Form.Group controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control {...email} placeholder="Enter email" />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control {...password} placeholder="Password" />
            </Form.Group>
          </Col>
        </Row>
        <Button
          variant="success"
          type="submit"
          onClick={useRegisterForm ? handleSubmit : handleLogin}
        >
          {useRegisterForm ? "Register" : "Log In"}
        </Button>
      </Form>
    </div>
  );
};

export default withRouter(LoginForm);
