import React, { useEffect, useState } from "react";
import grantService from "../services/grantService";
import requestService from "../services/requestService";
import { Doughnut } from "react-chartjs-2";
import { Bar } from "react-chartjs-2";

const Dashboard = () => {
  const [typeReport, setTypeReport] = useState(null);
  const [collegeReport, setCollegeReport] = useState(null);
  const [statusReport, setStatusReport] = useState(null);
  const [requestStatusReport, setRequestStatusReport] = useState(null);
  const [monthlySpendingReport, setMonthlySpendingReport] = useState(null);
  const [requestCountReport, setRequestCountReport] = useState(null);

  useEffect(() => {
    grantService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    requestService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    grantService
      .getReport("counter/college")
      .then((collegeData) => {
        const data = {
          datasets: [
            {
              barPercentage: 0.5,
              barThickness: 10,
              maxBarThickness: 15,
              minBarLength: 2,
              data: [
                collegeData["COB"],
                collegeData["COE"],
                collegeData["CAS"],
                collegeData["COL"],
                collegeData["CHS"],
                collegeData["MP"],
                collegeData["UC"]
              ],
              backgroundColor: [
                "green",
                "blue",
                "grey",
                "yellow",
                "blue",
                "red",
              ],
            },
          ],
          labels: [
            "College of Business",
            "College of Engineering",
            "College of Arts and Sciences ",
            "College of Law",
            "College of Health Sciences",
            "Military Programs",
            "University College"
          ],
        };

        setCollegeReport(data);
      })
      .then(
        grantService
          .getReport("counter/type")
          .then((typeData) => {
            const data = {
              datasets: [
                {
                  barPercentage: 0.5,
                  barThickness: 10,
                  maxBarThickness: 15,
                  minBarLength: 2,
                  data: [
                    typeData["Faculty Research Incentive Grant"],
                    typeData["Other"],
                    typeData["Teaching & Learning Grant"],
                    typeData["Action Research Grant"],
                  ],
                  backgroundColor: ["red", "blue", "green", "yellow"],
                },
              ],
              labels: [
                "Faculty Research Incentive Grant",
                "Other",
                "Teaching & Learning Grant",
                "Action Research Grant",
              ],
            };
            setTypeReport(data);
          })
          .catch((err) => console.log(err))
      )
      .then(
        grantService
          .getReport("counter/status")
          .then((statusData) => {
            const data = {
              datasets: [
                {
                  data: [statusData.Active, statusData.Expired],
                  backgroundColor: ["green", "red"],
                },
              ],

              labels: ["Active", "Expired"],
            };
            setStatusReport(data);
          })

          .catch((err) => console.log(err))
      )
      .then(
        requestService
          .counter("requeststatus")
          .then((requestStatusData) => {
            const data = {
              datasets: [
                {
                  data: [
                    requestStatusData.Approved,
                    requestStatusData.Denied,
                    requestStatusData.Pending,
                  ],
                  backgroundColor: ["green", "red", "yellow"],
                },
              ],

              labels: ["Approved", "Denied", "Pending"],
            };
            setRequestStatusReport(data);
          })
          .catch((err) => console.log(err))
      )
      .then(
        requestService
          .counter("requestcounter")
          .then((countData) => {
            const data = {
              datasets: [
                {
                  barPercentage: 0.5,
                  barThickness: 10,
                  maxBarThickness: 15,
                  minBarLength: 2,
                  data: [
                    countData.ResearchAssistantRequest,
                    countData.PurchaseRequest,
                    countData.ExpenseRequest,
                    countData.ReallocationRequest,
                  ],
                  backgroundColor: ["red", "blue", "green", "yellow"],
                },
              ],
              labels: [
                "RA Request",
                "Purchase Request",
                "Expense Request",
                "Reallocation Request",
              ],
            };
            setRequestCountReport(data);
          })
          .catch((err) => console.log(err))
      )
      .then(
        grantService
          .getReport("counter/monthlyspending")
          .then((spendingData) => {
            const data = {
              datasets: [
                {
                  barPercentage: 0.5,
                  barThickness: 10,
                  maxBarThickness: 15,
                  minBarLength: 2,
                  data: [
                    spendingData["Jan"],
                    spendingData["Feb"],
                    spendingData["Mar"],
                    spendingData["Apr"],
                    spendingData["May"],
                    spendingData["Jun"],
                    spendingData["Jul"],
                    spendingData["Aug"],
                    spendingData["Sep"],
                    spendingData["Oct"],
                    spendingData["Nov"],
                    spendingData["Dec"],
                  ],
                  backgroundColor: [
                    "red",
                    "blue",
                    "green",
                    "yellow",
                    "orange",
                    "purple",
                    "grey",
                    "indigo",
                    "red",
                    "blue",
                    "white",
                    "red",
                  ],
                },
              ],
              labels: [
                "Jan",
                "Feb",
                "Mar",
                "Apr",
                "May",
                "Jun",
                "Jul",
                "Aug",
                "Sep",
                "Oct",
                "Nov",
                "Dec",
              ],
            };

            setMonthlySpendingReport(data);
          })
          .catch((err) => console.log(err))
      )
      .catch((err) => console.log(err));
  }, []);
  return typeReport &&
    collegeReport &&
    statusReport &&
    requestStatusReport &&
    monthlySpendingReport &&
    requestCountReport ? (
    <div>
      <Doughnut
        data={statusReport}
        width={100}
        height={50}
        options={{
          title: {
            display: true,
            text: "Grant Status Count",
          },
        }}
      />
      <br />
      <br />
      <Bar
        data={collegeReport}
        width={30}
        height={10}
        options={{
          title: {
            display: true,
            text: "Grants By College",
          },
          legend: {
            display: false,
          },
        }}
      />
      <br />
      <br />
      <Bar
        data={typeReport}
        width={30}
        height={10}
        options={{
          title: {
            display: true,
            text: "Grants By Type",
          },
          legend: {
            display: false,
          },
        }}
      />
      <br />
      <br />
      <Bar
        data={requestCountReport}
        width={30}
        height={10}
        options={{
          title: {
            display: true,
            text: "Requests By Type",
          },
          legend: {
            display: false,
          },
        }}
      />
      <br />
      <br />
      <Bar
        data={monthlySpendingReport}
        width={30}
        height={10}
        type={"horizontalBar"}
        options={{
          title: {
            display: true,
            text: "Total Monthly Spending of Grants",
          },
          legend: {
            display: false,
          },
        }}
      />
      <br />
      <br />
      <Doughnut
        data={requestStatusReport}
        width={100}
        height={50}
        options={{
          title: {
            display: true,
            text: "Requests By Status",
          },
        }}
      />
    </div>
  ) : null;
};

export default Dashboard;
