import React, { useState, useEffect } from "react";
import userService from "../services/userService";
import Card from "react-bootstrap/Card";
import Accordion from "react-bootstrap/Accordion";
import Button from "react-bootstrap/Button";
import Spinner from "react-bootstrap/Spinner";
const AdminsList = () => {
  const [admins, setAdmins] = useState(null);

  useEffect(() => {
    userService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    const userType = JSON.parse(window.localStorage.getItem("loggedInUser"))
      .userType;
    userService
      .getAll(userType)
      .then((admins) => setAdmins(admins))
      .catch((err) => console.log(err));
  }, []);
  return (
    <div>
      <h3>Admins List</h3>
      {admins ? (
        <Accordion defaultActiveKey="0">
          {admins.map((admin) => (
            <Card>
              <Card.Header as="h5">
                <Accordion.Toggle
                  as={Button}
                  variant="link"
                  eventKey={admins.indexOf(admin)}
                >
                  {admin.name}
                </Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey={admins.indexOf(admin)}>
                <Card.Body>
                  <Card.Title>Email: {admin.email}</Card.Title>
                  <Card.Text>
                    <p>Gender: {admin.gender}</p>
                    <p>Location: {admin.location}</p>
                    <p>Role: {admin.role}</p>
                    <p>Phone Number: {admin.phoneNumber}</p>
                  </Card.Text>
                </Card.Body>
              </Accordion.Collapse>
            </Card>
          ))}
        </Accordion>
      ) : (
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      )}
    </div>
  );
};

export default AdminsList;
