import React, { useState } from "react";
import useField from "../../hooks/useField";

import axios from "axios";
import Form from "react-bootstrap/Form";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";

const ReallocationRequestForm = ({ submitReallocationRequest }) => {
  const from = useField();
  const to = useField();
  const justification = useField("text");
  const amount = useField("number");
  const [file, setFile] = useState(null);
  const [errMessage, setErrMessage] = useState("");

  const submitRequest = (event) => {
    event.preventDefault();
    const newReq = {
      from: from.value,
      to: to.value,
      amount: amount.value || 0,
      justification: justification.value,
    };
    if (file) {
      if (file.size > 10000000) {
        setErrMessage("File Must be under 10 MB");
        setTimeout(() => setErrMessage(""), 4000);
        return;
      } else if (
        file.type !==
          "application/vnd.openxmlformats-officedocument.wordprocessingml.document" &&
        file.type !==
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      ) {
        setErrMessage("File Must A Word or Excel File");
        setTimeout(() => setErrMessage(""), 4000);
        return;
      }
      onClickHandler();
      setFile(null);
    }
    submitReallocationRequest(newReq);
  };
  /*
    const { validated, errorStr } = validator();
    if (!validated) {
      setErrMessage(errorStr);
      setTimeout(() => setErrMessage(""), 4000);
    } else {
      submitRARequest(newReq);
    }
  };

  const validator = () => {
    let errorStr = "";
    if (!name.value || !salary.value || !startDate.value || !endDate.value) {
      errorStr += "Field is Missing";
      return { validated: false, errorStr };
    }

    if (name.value.length < 5) {
      errorStr += " Name must be at least 5 characters long";
      return { validated: false, errorStr };
    }

    return { validated: true, errorStr };
  };
  */
  const onChangeHandler = (event) => {
    console.log(event.target.files[0]);
    setFile(event.target.files[0]);
  };
  const onClickHandler = () => {
    const data = new FormData();
    data.append("file", file);
    axios
      .post("/upload", data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res) => {
        // then print response status
        console.log(res.statusText);
      })
      .catch((err) => console.log(err));
  };
  return (
    <Form>
      <h3>New Reallocation Request</h3>
      {errMessage ? <Alert variant={"danger"}>{errMessage}</Alert> : null}

      <Form.Group controlId="exampleForm.typeInput">
        <Form.Label>From Budget Category</Form.Label>
        <Form.Control as="select" onChange={from.onChange} value={from.value}>
          {!from.value ? (
            <option value="" disabled defaultValue>
              From Budget Category
            </option>
          ) : null}
          <option>Conference Budget</option>
          <option>RA Budget</option>
          <option>Software/Hardware Budget</option>
          <option>Other Item Budget</option>
        </Form.Control>
      </Form.Group>
      <Form.Group controlId="exampleForm.typeInput">
        <Form.Label>To Budget Category</Form.Label>
        <Form.Control as="select" onChange={to.onChange} value={to.value}>
          {!to.value ? (
            <option value="" disabled defaultValue>
              From Budget Category
            </option>
          ) : null}
          <option>Conference Budget</option>
          <option>RA Budget</option>
          <option>Software/Hardware Budget</option>
          <option>Other Item Budget</option>
        </Form.Control>
      </Form.Group>

      <Form.Row>
        <Form.Group as={Col} controlId="formGridName">
          <Form.Label>Amount</Form.Label>
          <Form.Control {...amount} placeholder="Enter Amount" />
        </Form.Group>

        <Form.Group as={Col} controlId="formGridSalary">
          <Form.Label>Justification</Form.Label>
          <Form.Control {...justification} placeholder="Enter Justification" />
        </Form.Group>
      </Form.Row>
      <label>Request File (optional)</label>
      <div className="input-group">
        <div className="custom-file">
          <input
            type="file"
            name="file"
            className="custom-file-input"
            id="inputGroupFile01"
            aria-describedby="inputGroupFileAddon01"
            onChange={onChangeHandler}
          />
          <label className="custom-file-label" htmlFor="inputGroupFile01">
            {file ? file.name : "Choose file"}
          </label>
        </div>
      </div>
      <Button variant="primary" type="submit" onClick={submitRequest}>
        Submit
      </Button>
    </Form>
  );
};

export default ReallocationRequestForm;
