import React, { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";
import Spinner from "react-bootstrap/Spinner";
import { Tab, Tabs } from "react-bootstrap";

import grantService from "../../services/grantService";
import GrantExpenses from "./GrantExpenses";
import GrantBudget from "./GrantBudget";
import GrantInfo from "./GrantInfo";
import GrantRequests from "./GrantRequests";
import CommentsList from "../Comments/CommentsList";

import { store } from "react-notifications-component";

const Grant = () => {
  const [grant, setGrant] = useState(null);
  const [comments, setComments] = useState(null);
  const [errorMessage, setErrorMessage] = useState(null);
  let { id } = useParams();
  const history = useHistory();

  useEffect(() => {
    grantService.setToken(
      JSON.parse(window.localStorage.getItem("loggedInUser")).token
    );
    grantService
      .getOne(id)
      .then((foundGrant) => {
        console.log(foundGrant);
        if (!foundGrant) {
          history.push("/");
          return;
        }
        setGrant(foundGrant);
        setComments(foundGrant.comments);
      })
      .catch((err) => console.log(err));
  }, []);

  const updateGrant = (updatedFields) => {
    const updatedGrant = {
      ...grant,
      ...updatedFields,
      pi: updatedFields.pi || grant.pi.id,
      coPi: updatedFields.coPi
        ? updatedFields.coPi
        : grant.coPi
        ? grant.coPi.id
        : null,
    };
    grantService
      .updateGrant(updatedGrant)
      .then((updatedGrant) => {
        setGrant(updatedGrant);
        store.addNotification({
          title: "Updated Grant!",
          message: "Success!",
          type: "success",
          insert: "top",
          container: "bottom-left",
          animationIn: ["animated", "fadeIn"],
          animationOut: ["animated", "fadeOut"],
          dismiss: {
            duration: 5000,
            onScreen: true,
          },
        });
      })
      .catch((err) => {
        console.log(err.response.data.error);
        setErrorMessage(err.response.data.error);
        setTimeout(() => setErrorMessage(""), 5000);
      });
  };
  const removeGrant = (id) => {
    const confirm = window.confirm("Are you sure?");
    if (confirm) {
      grantService
        .deleteGrant(id)
        .then((response) => {
          if (response === 204) {
            console.log("deleted");
            history.push("/grants");
          }
        })
        .catch((err) => console.log(err));
    }
  };

  if (grant) {
    return (
      <div>
        <h3 className="grantTitle">{grant.title} </h3>
        {errorMessage ? <Alert variant={"danger"}>{errorMessage}</Alert> : null}
        <Button
          variant="danger"
          size="sm"
          className="grantButton"
          onClick={() => {
            removeGrant(id);
          }}
        >
          Delete
        </Button>

        <Tabs defaultActiveKey="maininfo" id="uncontrolled-tab-example">
          <Tab eventKey="maininfo" title="Main Info">
            <GrantInfo grant={grant} updateGrant={updateGrant} />
          </Tab>
          <Tab eventKey="budget" title="Budget">
            <GrantBudget grant={grant} updateGrant={updateGrant} />
          </Tab>
          <Tab eventKey="expenses" title="Expenses">
            <GrantExpenses expenses={grant.expenses} />
          </Tab>
          <Tab eventKey="requests" title="Requests">
            <GrantRequests grant={grant} />
          </Tab>
        </Tabs>
        {comments ? (
          <CommentsList
            comments={comments ? comments : []}
            setComments={setComments}
            postId={grant._id}
            postType={"Grant"}
          />
        ) : (
          <Spinner animation="border" role="status">
            <span className="sr-only">Loading...</span>
          </Spinner>
        )}
      </div>
    );
  } else {
    return (
      <div>
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      </div>
    );
  }
};

export default Grant;
